import {
  AfterViewInit,
  ElementRef,
  Inject,
  Renderer2,
  OnChanges,
  SimpleChanges,
  Directive
} from '@angular/core';

@Directive()
export class UpTypographyBaseClass implements AfterViewInit, OnChanges {
  size: string;
  sizeClassesMap: Map<string, string>;
  defaultSize: string;

  constructor(
    @Inject(Renderer2) protected renderer: Renderer2,
    @Inject(ElementRef) protected hostElement: ElementRef
  ) {}

  ngAfterViewInit() {
    if (!this.size) {
      this.renderer.addClass(
        this.hostElement.nativeElement,
        this.sizeClassesMap.get(this.defaultSize)
      );
      return;
    }

    if (!this.isSizeValid(this.size)) {
      console.error(`${this.size} is not a valid size.`);
      return;
    }

    this.renderer.addClass(this.hostElement.nativeElement, this.sizeClassesMap.get(this.size));
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.size) {
      this.renderer.removeClass(
        this.hostElement.nativeElement,
        this.sizeClassesMap.get(changes.size.previousValue)
      );
      this.renderer.addClass(
        this.hostElement.nativeElement,
        this.sizeClassesMap.get(changes.size.currentValue)
      );
    }
  }

  private isSizeValid(size: string): boolean {
    return this.sizeClassesMap.has(size);
  }
}
