import { Directive, EventEmitter, Input, Output } from '@angular/core';
import { TableSort, TableSortDirection } from '../enums/table-sort.enum';

@Directive({
  selector: '[upTableSort]',
  exportAs: 'upTableSort'
})
export class TableSortDirective {
  @Input() defaultSortDirection = TableSortDirection.DESC;
  @Output() sortChanged: EventEmitter<TableSort> = new EventEmitter<TableSort>();

  set activeSort(sort: TableSort) {
    this._activeSort = sort;
    this.sortChanged.emit(sort);
  }

  get activeSort(): TableSort {
    return this._activeSort;
  }

  // tslint:disable-next-line:variable-name
  private _activeSort: TableSort;

  get sortName(): string {
    return this._activeSort?.name;
  }

  get sortDirection(): TableSortDirection {
    return this._activeSort?.direction;
  }

  constructor() {}

  setInitialSort(params: TableSort) {
    this._activeSort = params;
  }
}
