import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  Inject,
  Input,
  NgZone,
  OnDestroy,
  Renderer2
} from '@angular/core';
import { debounceTime, filter, fromEvent, map, mergeMap, Subject } from 'rxjs';
import { takeUntil, tap } from 'rxjs/operators';
import { Overlay, OverlayPositionBuilder, OverlayRef } from '@angular/cdk/overlay';
import {
  TooltipConstants,
  TooltipConstantsProvider,
  TooltipPositionType
} from '../../../tooltip/tooltip.constants';
import { ComponentPortal } from '@angular/cdk/portal';
import { WINDOW } from '../../../../providers/window.provider';
import { PdfPreviewCopyToClipboardTooltipComponent } from '../pdf-preview-copy-to-clipboard-tooltip/pdf-preview-copy-to-clipboard-tooltip.component';

@Component({
  selector: 'up-pdf-preview-copy-to-clipboard',
  template: ''
})
export class PdfPreviewCopyToClipboardComponent implements OnDestroy {
  @Input() parentElement: ElementRef;
  @Input() delay = 500;
  @Input() tooltipCloseDelay = 1000;
  @Input() copiedLabel = 'Copied';

  lastSelectedText: string;
  destroy$: Subject<void> = new Subject();

  private overlayRef: OverlayRef;
  private tooltipAutoCloseTimeout;

  constructor(
    private ngZone: NgZone,
    private renderer: Renderer2,
    private overlay: Overlay,
    private overlayPositionBuilder: OverlayPositionBuilder,
    private changeDetectorRef: ChangeDetectorRef,
    @Inject(WINDOW) private window: Window,
    @Inject(TooltipConstantsProvider) private tooltipConstants: TooltipConstants
  ) {}

  ngOnDestroy() {
    this.destroy$.next();
  }

  activateSelectionCopy() {
    this.ngZone.runOutsideAngular(() => {
      fromEvent(document, 'selectionchange')
        .pipe(
          debounceTime(this.delay),
          map(() => this.window.getSelection()?.toString()),
          filter((selection: string) => !!selection && this.lastSelectedText !== selection),
          filter(() => this.parentElement?.nativeElement.contains(this.getHighlightedNode())),
          tap((selection) => (this.lastSelectedText = selection)),
          takeUntil(this.destroy$)
        )
        .subscribe((selection) => {
          this.copySelectionToClipboard(selection);
        });
    });
  }

  private createTooltip(position: { x: number; y: number }) {
    if (this.tooltipAutoCloseTimeout) {
      clearTimeout(this.tooltipAutoCloseTimeout);
    }

    this.overlayRef?.dispose();
    this.getOverlay(position);

    const tooltipPortal = new ComponentPortal(PdfPreviewCopyToClipboardTooltipComponent);
    const tooltipRef = this.overlayRef.attach(tooltipPortal);
    tooltipRef.instance.content = this.copiedLabel;
    this.changeDetectorRef.detectChanges();

    this.tooltipAutoCloseTimeout = setTimeout(() => {
      this.overlayRef?.dispose();
    }, this.tooltipCloseDelay);
  }

  private getOverlay(position: Element | { x: number; y: number }) {
    const positionStrategy = this.overlayPositionBuilder
      .flexibleConnectedTo(position)
      .withPositions([
        this.tooltipConstants.positions.get(TooltipPositionType.BOTTOM),
        this.tooltipConstants.positions.get(TooltipPositionType.TOP)
      ]);

    this.overlayRef = this.overlay.create({
      positionStrategy,
      scrollStrategy: this.overlay.scrollStrategies.close()
    });
  }

  private copySelectionToClipboard(selection: string) {
    const element = this.getHighlightedNode();
    const rec = element?.getBoundingClientRect();
    this.ngZone.run(() => {
      this.window.navigator.clipboard.writeText(selection).then(() => {
        this.createTooltip({
          x: rec.x + this.window.getSelection()?.focusOffset,
          y: rec.y + rec.height
        });
      });
    });
  }

  private getHighlightedNode() {
    // @ts-ignore
    if (this.window.getSelection()?.focusNode?.getBoundingClientRect) {
      return this.window.getSelection().focusNode as HTMLElement;
    }

    return this.window.getSelection().focusNode.parentElement;
  }
}
