import { Component, Input, Inject, OnInit } from '@angular/core';
import { UpSidebarService } from '../../services/sidebar.service';
import { AutoUnsubscribeComponent } from '../../../../../src/lib/utils';
import { Subscription } from 'rxjs';

@Component({
  selector: 'up-sidebar-section',
  templateUrl: 'section-title.component.html',
  styleUrls: ['section-title.component.sass']
})
export class UpSidebarSectionTitleComponent extends AutoUnsubscribeComponent implements OnInit {
  collapsed = false;
  sidebarStateSubscription: Subscription;

  @Input() title: string;

  constructor(@Inject(UpSidebarService) private upSidebarService: UpSidebarService) {
    super();
  }

  ngOnInit(): void {
    this.sidebarStateSubscription = this.upSidebarService.toggleSidebar.subscribe(
      (state) => (this.collapsed = state)
    );
    this.addSubscriptions(this.sidebarStateSubscription);
  }

  get sectionTitleCssClass(): string {
    return this.collapsed ? 'up-section-title-collapsed' : 'up-section-title-default';
  }
}
