import {
  ChangeDetectionStrategy,
  Component,
  ComponentRef,
  EmbeddedViewRef,
  HostBinding,
  Inject,
  ViewChild
} from '@angular/core';
import { ModalContentType, ModalRef } from './modal-ref';
import { CdkPortalOutlet, ComponentPortal, TemplatePortal } from '@angular/cdk/portal';

export enum ModalContentTypes {
  Template = 'template',
  Component = 'component'
}

export enum ModalSize {
  fullScreen = 'full-screen'
}

const ModalSizeCSSClasses = {
  [ModalSize.fullScreen]: 'up-full-screen-modal'
};

@Component({
  selector: 'up-modal',
  templateUrl: './modal.component.html',
  changeDetection: ChangeDetectionStrategy.Default
})
export class UpModalComponent<T, D, V> {
  content: ModalContentType<T>;
  contentTypes = ModalContentTypes;

  @ViewChild(CdkPortalOutlet, { static: true }) portalOutlet: CdkPortalOutlet;

  @HostBinding('class') get hostCssClasses() {
    if (!this.customModalCSSClass) {
      return this.modalBaseCssClass;
    }
    return `${this.modalBaseCssClass} ${this.customModalCSSClass}`;
  }

  constructor(@Inject(ModalRef) private modalRef: ModalRef<T, D, V>) {
    this.content = this.modalRef.content;
  }

  get modalBaseCssClass() {
    return ModalSizeCSSClasses[this.modalRef.config.size] || 'up-modal';
  }

  get customModalCSSClass(): string {
    return this.modalRef.config.modalCSSClass || '';
  }

  close(): void {
    this.modalRef.close();
  }

  attachComponentPortal(portal: ComponentPortal<T>): ComponentRef<T> {
    return this.portalOutlet.attachComponentPortal(portal);
  }

  attachTemplatePortal<C>(portal: TemplatePortal<C>): EmbeddedViewRef<C> {
    return this.portalOutlet.attachTemplatePortal(portal);
  }
}
