import { InjectionToken } from '@angular/core';
import { PageNotificationConfig } from '../interfaces';

export const PageNotificationConfigProvider = new InjectionToken<PageNotificationConfig>(
  'pageNotificationConfigProvider'
);

export const pageNotificationDefaultConfig: PageNotificationConfig = {
  duration: 0,
  maxStack: 7
};

export const PageNotificationConfigFactory = () => ({
  ...pageNotificationDefaultConfig
});
