import { Component, Input, Inject } from '@angular/core';
import {
  SumsTypeProvider,
  SumCategory,
  SumType,
  SumTypes,
  SumLabelSizes
} from './sum-label.constants';

@Component({
  selector: 'up-sums',
  templateUrl: './sum-label.component.html',
  styleUrls: ['./sum-label.component.sass']
})
export class UpSumLabelComponent {
  @Input() type: SumType = SumCategory.DEFAULT;
  @Input() size: SumLabelSizes = SumLabelSizes.Base;

  constructor(@Inject(SumsTypeProvider) private provider: SumTypes) {}

  get cssClass(): string {
    return this.provider.types.get(this.type) || this.provider.types.get(SumCategory.DEFAULT);
  }
}
