import { Component, Input, Inject } from '@angular/core';
import {
  CustomLabelTypesProvider,
  CustomLabelTypes,
  CustomLabelCategory,
  CustomLabelType
} from './custom-label.constants';

@Component({
  selector: 'up-custom-label',
  templateUrl: './custom-label.component.html'
})
export class UpCustomLabelComponent {
  @Input() type: CustomLabelType = CustomLabelCategory.DEFAULT;
  @Input() content = '';

  constructor(@Inject(CustomLabelTypesProvider) private provider: CustomLabelTypes) {}

  get cssClass(): string {
    return (
      this.provider.labelTypes.get(this.type) ||
      this.provider.labelTypes.get(CustomLabelCategory.DEFAULT)
    );
  }
}
