import { Component, Input } from '@angular/core';
import { TitleSizes } from '../typography/title/title.directive';
import { ParagraphSize } from '../typography/paragraph/paragraph.directive';

export const AvatarSizes = {
  SMALL: 'small',
  DEFAULT: 'default',
  LARGE: 'large'
} as const;

export const AvatarTypes = {
  USER: 'user',
  COMPANY: 'company'
} as const;

export type AvatarSize = typeof AvatarSizes[keyof typeof AvatarSizes];
export type AvatarType = typeof AvatarTypes[keyof typeof AvatarTypes];

@Component({
  selector: 'up-avatar',
  templateUrl: 'avatar.component.html'
})
export class UpAvatarComponent {
  @Input() type: AvatarType = AvatarTypes.USER;
  @Input() logo?: string;
  @Input() size: AvatarSize = AvatarSizes.DEFAULT;
  @Input() initials?: string;
  @Input() darkMode = false;

  get isUserAvatar(): boolean {
    return this.type === AvatarTypes.USER;
  }

  get isCompanyAvatar(): boolean {
    return this.type === AvatarTypes.COMPANY;
  }

  get componentSizeClass(): string {
    switch (this.size) {
      case AvatarSizes.SMALL:
        return 'up-avatar-user-small';
      case AvatarSizes.LARGE:
        return 'up-avatar-user-large';
      default:
        return 'up-avatar-user-default';
    }
  }

  get textSize(): ParagraphSize {
    return this.size === AvatarSizes.SMALL ? TitleSizes.XXS : TitleSizes.S;
  }

  get darkModeClass(): string {
    return !!this.darkMode ? 'up-avatar-dark-mode' : '';
  }

  get avatarStyles(): { [key: string]: string } {
    return { 'background-image': `url('${this.logo}')` };
  }
}
