import { Component, EventEmitter, HostListener, Input, Output, TemplateRef } from '@angular/core';

@Component({
  selector: 'up-tooltip',
  templateUrl: './tooltip.component.html'
})
export class UpTooltipComponent {
  @Input()
  content: string | TemplateRef<void>;

  @Input()
  templateContextData?: object;

  @Output()
  mouseEnter: EventEmitter<void> = new EventEmitter<void>();

  @Output()
  mouseLeave: EventEmitter<void> = new EventEmitter<void>();

  get hasTemplateForContent(): boolean {
    return this.content instanceof TemplateRef;
  }

  @HostListener('mouseenter')
  enter(): void {
    this.mouseEnter.emit();
  }

  @HostListener('mouseleave')
  leave(): void {
    this.mouseLeave.emit();
  }

  get templateContext(): object {
    return { $implicit: this.templateContextData, ...this.templateContextData } || {};
  }
}
