import { Component, Inject, OnInit } from '@angular/core';
import { upIcon } from '../../icons/lib';
import { AutoUnsubscribeComponent } from '../../../src/lib/utils';
import { Subscription } from 'rxjs';

import { UpSidebarService } from './services/sidebar.service';

@Component({
  selector: 'up-sidebar',
  templateUrl: 'sidebar.component.html',
  styleUrls: ['sidebar.component.sass']
})
export class UpSidebarComponent extends AutoUnsubscribeComponent implements OnInit {
  collapsed = false;
  sidebarStateSubscription: Subscription;

  constructor(@Inject(UpSidebarService) private upSidebarService: UpSidebarService) {
    super();
  }

  ngOnInit(): void {
    this.sidebarStateSubscription = this.upSidebarService.toggleSidebar.subscribe(
      (state) => (this.collapsed = state)
    );
    this.addSubscriptions(this.sidebarStateSubscription);
  }

  toggleSidebar(): void {
    this.upSidebarService.changeSidebarState(!this.collapsed);
  }

  get sidebarCssClass(): string {
    return this.collapsed ? 'collapsed' : 'open';
  }

  get icon(): upIcon {
    return this.collapsed ? 'expand_medium' : 'collapse_medium';
  }
}
