import { upIcon } from '../icons/lib';
import { InjectionToken } from '@angular/core';

export const NotificationCategory = {
  INFO: 'info',
  SUCCESS: 'success',
  WARNING: 'warning',
  ERROR: 'error'
} as const;

export type NotificationType = typeof NotificationCategory[keyof typeof NotificationCategory];

export const NotificationModeType = {
  INLINE: 'inline',
  FLOATING: 'floating'
} as const;
export type NotificationMode = typeof NotificationModeType[keyof typeof NotificationModeType];

export type NotificationConfig = {
  containerCssClass: string;
  titleCssClass: string;
  textCssClass: string;
  icon: upIcon;
  iconColor: string;
  closeColor: string;
};

export interface Notifications {
  notificationTypes: Map<NotificationType, NotificationConfig>;
  notificationModes: Map<NotificationMode, string>;
  progressBarClassName: Map<NotificationType, string>;
  progressBarSupported: boolean;
}

export const NotificationsProvider = new InjectionToken<Notifications>('notificationsProvider');

const notificationTypes: Map<NotificationType, NotificationConfig> = new Map<
  NotificationType,
  NotificationConfig
>();

notificationTypes.set(NotificationCategory.INFO, {
  containerCssClass: 'up-notification-info',
  titleCssClass: 'up-notification-title-info',
  textCssClass: 'up-notification-text-info',
  icon: 'alert_info_small',
  iconColor: 'var(--up-brand-2)',
  closeColor: 'var(--up-brand-2)'
});

notificationTypes.set(NotificationCategory.WARNING, {
  containerCssClass: 'up-notification-warning',
  titleCssClass: 'up-notification-title-warning',
  textCssClass: 'up-notification-text-warning',
  icon: 'warning_icon',
  iconColor: 'var(--up-warning-900)',
  closeColor: 'var(--up-warning-900)'
});

notificationTypes.set(NotificationCategory.ERROR, {
  containerCssClass: 'up-notification-error',
  titleCssClass: 'up-notification-title-error',
  textCssClass: 'up-notification-text-error',
  icon: 'notification_error_small',
  iconColor: 'var(--up-negative-900)',
  closeColor: 'var(--up-negative-900)'
});

notificationTypes.set(NotificationCategory.SUCCESS, {
  containerCssClass: 'up-notification-success',
  titleCssClass: 'up-notification-title-success',
  textCssClass: 'up-notification-text-success',
  icon: 'alert_success_small',
  iconColor: 'var(--up-positive-900)',
  closeColor: 'var(--up-positive-900)'
});

const notificationModes: Map<NotificationMode, string> = new Map<NotificationMode, string>();
notificationModes.set(NotificationModeType.INLINE, 'up-notification-inline');
notificationModes.set(NotificationModeType.FLOATING, 'up-notification-floating');

const progressBarClassName: Map<NotificationType, string> = new Map<NotificationType, string>();
progressBarClassName.set(NotificationCategory.INFO, 'up-progress-bar-info');
progressBarClassName.set(NotificationCategory.WARNING, 'up-progress-bar-warning');
progressBarClassName.set(NotificationCategory.ERROR, 'up-progress-bar-error');
progressBarClassName.set(NotificationCategory.SUCCESS, 'up-progress-bar-success');

export const NotificationsFactory = () => ({
  notificationTypes,
  notificationModes,
  progressBarClassName,
  progressBarSupported: true
});
