import { Injectable } from '@angular/core';
import { TableDisplayDensityEnum } from '../enums/table-display-density.enum';
import { BehaviorSubject, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class TableDisplayDensityService {
  displayDensityMode$: Subject<TableDisplayDensityEnum> = new BehaviorSubject(
    TableDisplayDensityEnum.REGULAR
  );

  constructor() {}

  setDisplayDensity(mode: TableDisplayDensityEnum) {
    this.displayDensityMode$.next(mode);
  }

  getDisplayDensity(): Subject<TableDisplayDensityEnum> {
    return this.displayDensityMode$;
  }
}
