import { Component, HostBinding, Input, TemplateRef, ViewEncapsulation } from '@angular/core';
import { upIcon } from '../../icons/lib';

@Component({
  selector: 'up-top-bar',
  templateUrl: 'topbar.component.html',
  styleUrls: ['topbar.component.sass'],
  encapsulation: ViewEncapsulation.None
})
export class UpTopBarComponent {
  @HostBinding('class.UpTopBarComponent') containerClass = true;
  chevronIcon: upIcon = upIcon.chevron_down_small;

  @Input() avatarInitials = '';
  @Input() notificationCount = '0';
  @Input() logo = '';
  @Input() showTopBarAmbassadorSeparator = false;
  @Input() userMenuTemplate: TemplateRef<void>;

  updateChevronIcon(isUserMenuOpen: boolean): void {
    this.chevronIcon = isUserMenuOpen ? upIcon.chevron_up_small : upIcon.chevron_down_small;
  }
}
