import {
  Component,
  EventEmitter,
  HostBinding,
  HostListener,
  Input,
  Optional,
  Output
} from '@angular/core';
import { BaseTableRowColumnDirective } from '../../helpers/base-table-row-column';
import { TableSortDirection } from '../../enums/table-sort.enum';
import { TableSortDirective } from '../../directives';
import { TableCheckboxDirective } from '../../directives/table-checkbox.directive';

@Component({
  // tslint:disable-next-line:component-selector
  selector: '[upTableTh]',
  exportAs: 'upTableTh',
  templateUrl: './table-th.component.html'
})
export class TableThComponent extends BaseTableRowColumnDirective {
  readonly DIRECTIONS = TableSortDirection;

  @Input() dataTest = 'filter-column-icon';
  @Input() width: number;
  @Input() hasSuffixIcon = false;
  @Input() hasPrefixIcon = false;

  @Input() sortName: string;
  @Input() hasFilter = false;

  @Output() filterOpened: EventEmitter<void> = new EventEmitter<void>();
  @Output() filterClosed: EventEmitter<void> = new EventEmitter<void>();

  @HostBinding('class.up-table-th') upTableThClass = true;
  @HostBinding('class.up-table-th--with-sort') get upTableThSortable() {
    return !!this.sortName;
  }

  @HostBinding('class.has-suffix-icon') get canFilterClass() {
    return this.hasSuffixIcon || this.hasFilter;
  }
  @HostBinding('class.has-prefix-icon') get sortActiveClass() {
    return this.hasPrefixIcon || !!this.sortName;
  }
  @HostBinding('style.max-width') get maxWidth() {
    return !!this.width ? `${this.width}px` : 'auto';
  }

  @HostListener('click') handleClick(): void {
    if (!this.sortName || !this.sortDirective) {
      return;
    }

    const activeSort = this.sortDirective.activeSort;
    if (!activeSort) {
      this.sortDirective.activeSort = {
        name: this.sortName,
        direction: this.sortDirective.defaultSortDirection
      };
      return;
    }

    if (activeSort.name !== this.sortName) {
      this.sortDirective.activeSort = {
        name: this.sortName,
        direction: this.sortDirective.defaultSortDirection
      };
      return;
    }

    if (activeSort.direction === this.sortDirective.defaultSortDirection) {
      const direction =
        this.sortDirective.defaultSortDirection === TableSortDirection.ASC
          ? TableSortDirection.DESC
          : TableSortDirection.ASC;
      this.sortDirective.activeSort = { name: this.sortName, direction };
    } else {
      this.sortDirective.activeSort = null;
    }
  }

  constructor(
    @Optional() public sortDirective: TableSortDirective,
    @Optional() public tableCheckboxDirective: TableCheckboxDirective
  ) {
    super();
  }

  openFilter(): void {
    this.filterOpened.emit();
  }

  closeFilter() {
    this.filterClosed.emit();
  }
}
