import { InjectionToken } from '@angular/core';

export const CustomLabelCategory = {
  NEW: 'new',
  UPDATED: 'updated',
  ERROR: 'error',
  WARNING: 'warning',
  POSITIVE: 'positive',
  GENERIC: 'generic',
  NEUTRAL: 'neutral',
  DEFAULT: 'default'
};

export type CustomLabelType = typeof CustomLabelCategory[keyof typeof CustomLabelCategory];

export interface CustomLabelTypes {
  labelTypes: Map<CustomLabelType, string>;
}

export const CustomLabelTypesProvider = new InjectionToken<CustomLabelTypes>(
  'customLabelTypesProvider'
);

const labelTypes: Map<CustomLabelType, string> = new Map<CustomLabelType, string>();
labelTypes.set(CustomLabelCategory.NEW, 'up-custom-label-new');
labelTypes.set(CustomLabelCategory.UPDATED, 'up-custom-label-updated');
labelTypes.set(CustomLabelCategory.ERROR, 'up-custom-label-error');
labelTypes.set(CustomLabelCategory.WARNING, 'up-custom-label-warning');
labelTypes.set(CustomLabelCategory.POSITIVE, 'up-custom-label-positive');
labelTypes.set(CustomLabelCategory.GENERIC, 'up-custom-label-generic');
labelTypes.set(CustomLabelCategory.NEUTRAL, 'up-custom-label-neutral');
labelTypes.set(CustomLabelCategory.DEFAULT, 'up-custom-label-default');

export const CustomLabelTypesFactory = () => ({
  labelTypes
});
