import {
  AfterContentInit,
  Component,
  ContentChildren,
  ElementRef,
  Inject,
  Input,
  NgZone,
  OnChanges,
  OnDestroy,
  OnInit,
  Renderer2,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { TableBodyDirective, TableHeadDirective } from '../../directives';
import { TableDisplayDensityEnum } from '../../enums';
import { RESIZE_OBSERVER, ResizeObserverRef, TableDisplayDensityService } from '../../services';

@Component({
  selector: 'up-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.sass'],
  encapsulation: ViewEncapsulation.None
})
export class TableComponent implements AfterContentInit, OnInit, OnDestroy, OnChanges {
  @Input() displayDensityMode: TableDisplayDensityEnum;

  @ContentChildren(TableBodyDirective, { descendants: true, read: ElementRef }) tbody;
  @ContentChildren(TableHeadDirective, { descendants: true, read: ElementRef }) thead;

  resizeObserver: ResizeObserver;

  tbodyElement: HTMLElement;
  theadElement: HTMLElement;

  displayDensityMode$: Subject<TableDisplayDensityEnum>;
  tableDisplayDensityEnum = TableDisplayDensityEnum;

  constructor(
    @Inject(Renderer2) private renderer: Renderer2,
    @Inject(NgZone) private ngZone: NgZone,
    @Inject(TableDisplayDensityService)
    private tableDisplayDensityService: TableDisplayDensityService,
    @Inject(RESIZE_OBSERVER)
    private resizeObserverRef: ResizeObserverRef
  ) {}

  ngOnInit() {
    if (this.displayDensityMode) {
      this.displayDensityMode$ = new BehaviorSubject(this.displayDensityMode);
      return;
    }

    this.displayDensityMode$ = this.tableDisplayDensityService.getDisplayDensity();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.displayDensityMode) {
      this.displayDensityMode$?.next(changes.displayDensityMode.currentValue);
    }
  }

  ngAfterContentInit() {
    this.tbodyElement = this.tbody.toArray()[0]?.nativeElement;
    this.theadElement = this.thead.toArray()[0]?.nativeElement;
    this.compensateTbodyScrollDimension();
  }

  ngOnDestroy() {
    this.resizeObserver?.unobserve(this.tbodyElement);
  }

  private compensateTbodyScrollDimension() {
    if (!this.theadElement || !this.tbodyElement) {
      return;
    }

    this.ngZone.runOutsideAngular(() => {
      this.resizeObserver = this.resizeObserverRef.resizeObserver(this.onTheadSizeChange);
      this.resizeObserver.observe(this.tbodyElement);
    });
  }

  private onTheadSizeChange = (entries) => {
    if (!entries[0]?.contentRect.width) {
      return;
    }

    const diff = this.getTableHeadWithDifference(entries[0].contentRect.width);

    if (diff > 0) {
      this.renderer.setStyle(this.theadElement, `padding-right`, `${diff}px`);
    } else if (diff < 0) {
      this.renderer.setStyle(this.theadElement, `padding-right`, `0px`);
    }
  };

  private getTableHeadWithDifference(bodyWidth: number): number {
    const headStyle = getComputedStyle(this.theadElement);
    const headWidth = parseInt(headStyle.width, 10);
    const headRightPadding = parseInt(headStyle.paddingRight, 10);
    return headWidth - headRightPadding - bodyWidth;
  }
}
