import { Pipe, PipeTransform } from '@angular/core';
import Big from 'big.js';
import { isNaN } from 'lodash';
import { formatNumber } from 'accounting';
import { DecimalSeparatorService } from '../services/decimal-separator.service';

@Pipe({
  name: 'upNumber'
})
export class NumberPipe implements PipeTransform {
  MAX_DECIMALS = 20;

  constructor(private decimalSeparatorService: DecimalSeparatorService) {}

  transform(value: any, numberOfDecimals?: number): string {
    if (!isNaN(parseFloat(value))) {
      const viewValue = Big(value);
      let stringValue = viewValue.toString();
      let fractional;

      if (numberOfDecimals || numberOfDecimals === 0) {
        stringValue = viewValue.toFixed(numberOfDecimals);
        fractional = stringValue.split('.')[1];
      } else {
        fractional = stringValue.split('.')[1];
        fractional =
          fractional && fractional.length > this.MAX_DECIMALS
            ? this.roundIntegerPart(fractional)
            : fractional;
      }

      let decimal = stringValue.split('.')[0];
      if (decimal !== '-0') {
        decimal = formatNumber(
          decimal,
          0,
          this.decimalSeparatorService.thousandSeparator,
          this.decimalSeparatorService.separator
        );
      }

      return fractional
        ? `${decimal}${this.decimalSeparatorService.separator}${fractional}`
        : `${decimal}`;
    }
  }

  private roundIntegerPart(value): string {
    return Big(`${value.substring(0, 20)}.${value.substring(20)}`)
      .round()
      .toString();
  }
}
