import {
  Component,
  EventEmitter,
  HostBinding,
  Inject,
  Input,
  OnInit,
  Output,
  ViewEncapsulation
} from '@angular/core';
import { upIcon } from '../../../../icons/lib';
import { AutoUnsubscribeComponent } from '../../../../../src/lib/utils';
import { UpSidebarService } from '../../services/sidebar.service';
import { Subscription } from 'rxjs';
import { TooltipTrigger, TooltipTriggerType } from '../../../../tooltip/tooltip.constants';

@Component({
  selector: 'up-menu-item',
  styleUrls: ['menu-item.component.sass'],
  templateUrl: 'menu-item.component.html',
  encapsulation: ViewEncapsulation.None
})
export class UpSidebarMenuItemComponent extends AutoUnsubscribeComponent implements OnInit {
  collapsed = false;
  sidebarStateSubscription: Subscription;
  placeholderIcon = 'alert_help_medium';

  @HostBinding('class.UpSidebarMenuItemComponent') containerClass = true;
  @Input() selected = false;
  @Input() count = '';
  @Input() icon: string | upIcon;
  @Input() disabled = false;
  @Input() greyedOut = false;
  @Input() popover = '';
  @Input() dataTest = '';
  @Input() persistTooltip = false;

  @Output() handleClick: EventEmitter<void> = new EventEmitter<void>();

  constructor(@Inject(UpSidebarService) private upSidebarService: UpSidebarService) {
    super();
  }

  ngOnInit(): void {
    this.sidebarStateSubscription = this.upSidebarService.toggleSidebar.subscribe(
      (state) => (this.collapsed = state)
    );
    this.addSubscriptions(this.sidebarStateSubscription);
  }

  get menuItemContainerCssClass(): string {
    return this.collapsed ? 'up-menu-item-container-collapsed' : 'up-menu-item-container';
  }

  get selectedItemCssClass(): string {
    return this.selected ? 'up-menu-selected' : 'up-menu-default';
  }

  get hasBadge(): boolean {
    return !!this.count;
  }

  get isTooltipDisabled(): boolean {
    return !this.popover || !(this.disabled || this.collapsed);
  }
}
