import {
  Component,
  Input,
  Inject,
  OnInit,
  Output,
  EventEmitter,
  TemplateRef,
  OnChanges,
  SimpleChanges
} from '@angular/core';
import { AutoUnsubscribeComponent } from '../../../../../src/lib/utils';
import { Subscription } from 'rxjs';
import { UpSidebarService } from '../../services/sidebar.service';
import { getLongestWord } from '../../../../../src/lib/helpers/strings/get-longest-word';
import { UpIcon } from '../../../../icons/lib';

@Component({
  selector: 'up-sidebar-header',
  templateUrl: 'header.component.html',
  styleUrls: ['header.component.sass']
})
export class UpSidebarHeaderComponent
  extends AutoUnsubscribeComponent
  implements OnInit, OnChanges {
  collapsed = false;
  sidebarStateSubscription: Subscription;

  @Input() companyName: string;
  @Input() companyId: string;
  @Input() tooltip: string;
  @Input() companyInitials: string;
  @Input() companyLocation: string;
  @Input() chevronToggled = false;
  @Input() showCompanySwitcher = false;
  @Input() companySwitcherTemplate: TemplateRef<void>;
  @Output() toggleDropdown: EventEmitter<boolean> = new EventEmitter<boolean>();

  hasLongCompanyName = false;

  private MAX_CHARACTERS_WORD_ALLOWED = 20;

  constructor(@Inject(UpSidebarService) private upSidebarService: UpSidebarService) {
    super();
  }

  ngOnInit(): void {
    this.sidebarStateSubscription = this.upSidebarService.toggleSidebar.subscribe(
      (state) => (this.collapsed = state)
    );
    this.addSubscriptions(this.sidebarStateSubscription);
    this.setHasLongCompanyName();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.companyName) {
      this.setHasLongCompanyName();
    }
  }

  get userCompanyDetailsCssClass(): string {
    return this.collapsed ? 'up-user-company-details-collapsed' : 'up-user-data-container-default';
  }

  handleChevronToggle(): void {
    this.chevronToggled = !this.chevronToggled;
    this.toggleDropdown.emit(this.chevronToggled);
  }

  get showRightSideTemplate(): boolean {
    return this.companySwitcherTemplate && !this.showCompanySwitcher && !this.collapsed;
  }

  get chevronIcon(): UpIcon['name'] {
    return this.chevronToggled ? 'chevron_up_small' : 'chevron_down_small';
  }

  private setHasLongCompanyName() {
    this.hasLongCompanyName = getLongestWord(this.companyName) > this.MAX_CHARACTERS_WORD_ALLOWED;
  }
}
