import { Component, Input, Output, EventEmitter } from '@angular/core';
import { upIcon } from '../icons/lib';

@Component({
  selector: 'up-upload-panel',
  templateUrl: 'upload-panel.component.html',
  styleUrls: ['upload-panel.component.sass']
})
export class UploadPanelComponent {
  statusHeaderOpened = false;

  @Input()
  statusHeaderMessage: string;

  @Input()
  remainingTimeInfo: string;

  @Input()
  cancelUploadText: string;

  @Input()
  uploadProcessCompletedSuccessfully = false;

  @Output()
  closePanel: EventEmitter<void> = new EventEmitter<void>();

  @Output()
  cancelUploads: EventEmitter<void> = new EventEmitter<void>();

  toggleStatusHeader(): void {
    this.statusHeaderOpened = !this.statusHeaderOpened;
  }

  get statusHeaderCSSClass(): string {
    return this.statusHeaderOpened ? 'up-upload-status-header' : 'up-upload-status-header-rounded';
  }

  get statusHeaderChevronIcon(): upIcon {
    return this.isStatusHeaderOpened ? 'chevron_down_medium' : 'chevron_up_medium';
  }

  get isStatusHeaderOpened(): boolean {
    return this.statusHeaderOpened === true;
  }

  get isUploadProcessCompletedSuccessfully(): boolean {
    return this.uploadProcessCompletedSuccessfully === true;
  }

  handleClosePanel(): void {
    this.closePanel.emit();
  }

  handleCancelUploads(): void {
    this.cancelUploads.emit();
  }
}
