import { Component, Input, Inject } from '@angular/core';
import { TitleSize } from '../../../typography/title/title.directive';
import {
  NotificationsProvider,
  Notifications,
  NotificationCategory,
  NotificationType
} from '../../notification.constants';

@Component({
  selector: 'up-notification-title',
  templateUrl: 'notification-title.component.html'
})
export class UpNotificationTitleComponent {
  @Input() baseDataTest = 'up-notification';

  @Input()
  type: NotificationType = NotificationCategory.INFO;

  @Input()
  title: string;

  @Input()
  size: TitleSize = 'XS';

  constructor(@Inject(NotificationsProvider) private provider: Notifications) {}

  get notificationTitleCssClass(): string {
    return this.provider.notificationTypes.get(this.type).titleCssClass;
  }
}
