import { Directive, HostListener, ElementRef, OnInit, Input } from '@angular/core';
import { NgControl } from '@angular/forms';
import { IbanPipe } from '../pipes/iban.pipe';

@Directive({
  selector: '[upIBAN]',
  providers: [IbanPipe]
})
export class IbanMaskDirective implements OnInit {
  @Input() autocompleteCountryCodeAndChecksum = false;
  @Input() countryCode: string;

  constructor(
    private element: ElementRef,
    private ibanPipe: IbanPipe,
    private control: NgControl
  ) {}

  ngOnInit() {
    if (this.control && this.control.value) {
      this.updateValue(this.control.value);
    }
  }

  @HostListener('input', ['$event.target.value'])
  inputChange(value) {
    this.updateValue(value);
  }

  private updateValue(value) {
    if (value) {
      this.element.nativeElement.value = this.ibanPipe.transform(
        value,
        this.autocompleteCountryCodeAndChecksum,
        this.countryCode
      );
      this.control.control.setValue(
        this.ibanPipe.parse(value, this.autocompleteCountryCodeAndChecksum, this.countryCode),
        {
          onlySelf: false,
          emitEvent: false,
          emitModelToViewChange: false,
          emitViewToModelChange: false
        }
      );
    }
  }
}
