import { OnChanges, SimpleChanges, Directive } from '@angular/core';

@Directive()
export abstract class UpBaseListPaginationComponent<T> implements OnChanges {
  items: T[] = [];
  pageNumber = 1;
  pageSize = 10;

  get currentPageItems(): T[] {
    return this.items.slice((this.pageNumber - 1) * this.pageSize, this.pageNumber * this.pageSize);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.hasOwnProperty('items')) {
      this.pageNumber = 1;
    }
  }

  pageNumberChange(pageNumber: number): void {
    this.pageNumber = pageNumber;
  }
}
