import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'up-user-details',
  templateUrl: './user-details.component.html',
  styleUrls: ['./user-details.component.sass']
})
export class UserDetailsComponent {
  @Input() name = '';
  @Input() emailAddress = '';
  @Input() logo = '';
  @Input() userInitials = '';
  @Input() showChangeAvatarButton = true;

  @Output() changeAvatarClicked: EventEmitter<void> = new EventEmitter();

  handleChangeAvatarClicked(): void {
    this.changeAvatarClicked.emit();
  }
}
