import { Directive, HostBinding, Input } from '@angular/core';
import { TableAlign, TableAlignType } from '../enums';

@Directive()
export class BaseTableRowColumnDirective {
  @Input() alignDirection: TableAlignType = TableAlign.LEFT;

  @HostBinding('class.table-align-right') get tableAlignRightClass() {
    return this.alignDirection === TableAlign.RIGHT;
  }
}
