import {
  AfterViewInit,
  Directive,
  ElementRef,
  EventEmitter,
  HostListener,
  Injector,
  Input,
  Output,
  TemplateRef,
  ViewContainerRef
} from '@angular/core';
import {
  FlexibleConnectedPositionStrategy,
  Overlay,
  OverlayConfig,
  OverlayRef
} from '@angular/cdk/overlay';
import { ComponentPortal } from '@angular/cdk/portal';

import { AutoUnsubscribeComponent, KeyboardKeysEnum } from '../../src/lib/utils';
import { popOutPositions, dropdownPositionsMap } from './constants';
import { UpPopOutService } from './pop-out.service';
import { FLOATING_CARD_PADDING, FLOATING_CARD_TOKEN } from '../floating-card/constants';
import { UpFloatingCardComponent } from '../floating-card/floating-card.component';

@Directive({
  selector: '[upPopOutTrigger]',
  exportAs: 'upPopOutTrigger'
})
export class UpPopOutTriggerDirective extends AutoUnsubscribeComponent implements AfterViewInit {
  @Input('upPopOutTrigger') contentTemplate: TemplateRef<any>;
  @Output() popoutOpened: EventEmitter<void> = new EventEmitter();
  @Output() popoutClosed: EventEmitter<void> = new EventEmitter();
  @Input() position: popOutPositions = popOutPositions.BottomLeft;
  @Input() closeOnOptionClick = true;
  @Input() popoutDisabled = false;
  @Input() stopClickEventPropagation = false;
  @Input() hasFloatingCardPadding = true;

  overlayRef: OverlayRef | null;

  @HostListener('click', ['$event'])
  openMenu(event: MouseEvent): void {
    if (this.stopClickEventPropagation) {
      event?.preventDefault();
      event?.stopPropagation();
    }
    this.open();
  }

  constructor(
    private overlay: Overlay,
    private viewContainerRef: ViewContainerRef,
    private hostElement: ElementRef,
    private upPopOutService: UpPopOutService
  ) {
    super();
  }

  ngAfterViewInit(): void {
    if (!this.hasValidSetup) {
      console.warn(
        'upPopOutTrigger needs a valid TemplateRef to work. Use a #templateVariable on an ng-template'
      );
    }
  }

  open(): void {
    if (this.popoutDisabled) {
      return;
    }

    if (!this.hasValidSetup) {
      return;
    }

    this.overlayRef = this.overlay.create(this.overlayConfig);

    this.overlayRef.attach(this.createPortal());

    this.popoutOpened.emit();

    this.subscribeToCloseEvents();
  }

  close(): void {
    this.overlayRef.dispose();
    this.unsubscribeAll();
    this.popoutClosed.emit();
  }

  private subscribeToCloseEvents(): void {
    this.addSubscriptions(
      this.upPopOutService.optionClick.subscribe(() => this.closeOnOptionClick && this.close()),
      this.overlayRef.backdropClick().subscribe(() => this.close()),
      this.overlayRef.keydownEvents().subscribe((keydownEvent: KeyboardEvent) => {
        if (keydownEvent.key === KeyboardKeysEnum.Escape) {
          this.close();
        }
      })
    );
  }

  private get overlayConfig(): OverlayConfig {
    return {
      positionStrategy: this.positionStrategy,
      hasBackdrop: true,
      backdropClass: 'up-pop-out-backdrop',
      scrollStrategy: this.overlay.scrollStrategies.block()
    };
  }

  private get positionStrategy(): FlexibleConnectedPositionStrategy {
    return this.overlay
      .position()
      .flexibleConnectedTo(this.hostElement)
      .withPositions([dropdownPositionsMap.get(this.position), ...dropdownPositionsMap.values()]);
  }

  private createPortal(): ComponentPortal<UpFloatingCardComponent> {
    return new ComponentPortal(
      UpFloatingCardComponent,
      null,
      Injector.create({
        providers: [
          {
            provide: FLOATING_CARD_TOKEN,
            useValue: this.contentTemplate
          },
          {
            provide: FLOATING_CARD_PADDING,
            useValue: this.hasFloatingCardPadding
          }
        ]
      })
    );
  }

  private get hasValidSetup(): boolean {
    return this.contentTemplate instanceof TemplateRef;
  }
}
