import { Injectable } from '@angular/core';
import { Separator } from '../enum/separator.enum';

@Injectable({
  providedIn: 'root'
})
export class DecimalSeparatorService {
  private decimalSeparator: Separator = Separator.DOT;

  constructor() {}

  set separator(separator: Separator) {
    this.decimalSeparator = separator;
  }

  get separator(): Separator {
    return this.decimalSeparator;
  }

  get thousandSeparator(): Separator {
    return this.decimalSeparator === Separator.DOT ? Separator.COMMA : Separator.DOT;
  }
}
