import {
  Component,
  ContentChild,
  EventEmitter,
  Inject,
  Input,
  Output,
  SecurityContext,
  ViewEncapsulation
} from '@angular/core';
import { upIcon } from '../icons/lib';
import {
  NotificationCategory,
  NotificationConfig,
  NotificationMode,
  NotificationModeType,
  Notifications,
  NotificationsProvider,
  NotificationType
} from './notification.constants';
import { DomSanitizer } from '@angular/platform-browser';
import { UpNotificationTitleComponent } from './components/title/notification-title.component';

@Component({
  selector: 'up-notification',
  templateUrl: 'notification.component.html',
  encapsulation: ViewEncapsulation.None
})
export class UpNotificationComponent {
  @Input() baseDataTest = 'up-notification';

  @Input()
  type: NotificationType = NotificationCategory.INFO;

  @Input()
  text = '';

  @Input()
  closeable = false;

  @Input()
  mode: NotificationMode = NotificationModeType.INLINE;

  @Input()
  progressBarDuration: number | undefined;

  @Input()
  isOpaque = false;

  @Output()
  closeNotification: EventEmitter<MouseEvent> = new EventEmitter<MouseEvent>();
  @ContentChild(UpNotificationTitleComponent)
  upNotificationTitleComponent?: UpNotificationTitleComponent;

  constructor(
    @Inject(NotificationsProvider) private provider: Notifications,
    private sanitizer: DomSanitizer
  ) {}

  get sanitizedText() {
    return this.sanitizer.sanitize(SecurityContext.HTML, this.text);
  }

  get config(): NotificationConfig {
    return (
      this.provider.notificationTypes.get(this.type) ||
      this.provider.notificationTypes.get(NotificationCategory.INFO)
    );
  }

  get containerCssClass(): string {
    return this.isOpaque ? 'up-notification-opaque' : this.config.containerCssClass;
  }

  get icon(): upIcon {
    return this.config.icon;
  }

  get iconColor(): string {
    return this.config.iconColor;
  }

  get closeColor(): string {
    return this.config.closeColor;
  }

  get textCssClass(): string {
    return this.config.textCssClass;
  }

  get modeClass(): string {
    return this.provider.notificationModes.get(this.mode);
  }

  get iconSize(): string {
    return this.mode === NotificationModeType.INLINE ? '16px' : '20px';
  }

  get hasInlineMode(): boolean {
    return this.mode === NotificationModeType.INLINE;
  }
}
