import { Pipe, PipeTransform } from '@angular/core';
import * as IBAN from 'iban';

export enum COUNTRY_CODE_CHECKSUM_SUFFIX {
  RS = 'RS35'
}

@Pipe({
  name: 'IBAN'
})
export class IbanPipe implements PipeTransform {
  transform(value: any, autocompleteCountryCodeAndChecksum = false, countryCode = ''): string {
    if (!value) {
      return '';
    }

    return IBAN.printFormat(this.cleanIban(value, autocompleteCountryCodeAndChecksum, countryCode));
  }

  parse(value: any, autocompleteCountryCodeAndChecksum = false, countryCode = ''): string {
    if (!value) {
      return '';
    }
    const iban: string = this.cleanIban(value, autocompleteCountryCodeAndChecksum, countryCode);

    return iban;
  }

  private cleanIban(
    value: string,
    autocompleteCountryCodeAndChecksum = false,
    countryCode = ''
  ): string {
    let iban: string;

    if (
      autocompleteCountryCodeAndChecksum &&
      countryCode &&
      COUNTRY_CODE_CHECKSUM_SUFFIX[countryCode] &&
      ((value.length === 1 && !this.onlyLetters(value)) || IBAN.isValidBBAN(countryCode, value))
    ) {
      iban = IBAN.electronicFormat(`${COUNTRY_CODE_CHECKSUM_SUFFIX[countryCode]}${value}`);
    } else {
      iban = IBAN.electronicFormat(value);
    }

    if (iban.length < 3 && !this.onlyLetters(iban[iban?.length - 1])) {
      return this.removeLast(iban);
    }

    if (iban.length < 3) {
      return value;
    }

    const country = iban.substr(0, 2);
    if (!IBAN.countries.hasOwnProperty(country)) {
      return country;
    }

    const countrySpecification = IBAN.countries[country];
    if (iban.length > countrySpecification.length) {
      return iban.substr(0, countrySpecification.length);
    }

    return iban;
  }

  private onlyLetters(value: string): boolean {
    if (!value) {
      return true;
    }
    return value.toLowerCase() !== value.toUpperCase();
  }

  private removeLast(value: string): string {
    if (!value) {
      return '';
    }

    return value.substr(0, value.length - 1);
  }
}
