import { Injectable } from '@angular/core';
import { UpDateFormatType } from '../constants/date-format-type';

@Injectable({
  providedIn: 'root'
})
export class DateFormatService {
  private dateFormat$: UpDateFormatType = 'dd-MM-yyyy';
  private delimiter = '-';

  constructor() {}

  set dateFormat(format: UpDateFormatType) {
    if (this.isValidFormat(format)) {
      this.dateFormat$ = format;
      return;
    }

    const normalisedDateFormat = format.replace(/[^M]/g, (txt) => txt.toLowerCase());
    if (this.isValidFormat(normalisedDateFormat)) {
      this.dateFormat$ = normalisedDateFormat as UpDateFormatType;
      return;
    }
  }

  get dateFormat(): UpDateFormatType {
    return this.dateFormat$ || (`dd${this.delimiter}MM${this.delimiter}yyyy` as UpDateFormatType);
  }

  private isValidFormat(format: string): boolean {
    return ['dd-MM-yyyy', 'MM-dd-yyyy', 'yyyy-MM-dd', 'dd/MM/yyyy', 'MM/dd/yyyy'].includes(format);
  }
}
