import { AfterViewInit, Directive, Input, OnChanges, SimpleChanges } from '@angular/core';
import { UpTypographyBaseClass } from '../typography-base-class/typography.class';

export const ParagraphSizes = {
  XXS: 'XXS',
  XS: 'XS',
  S: 'S',
  Base: 'base'
} as const;
export type ParagraphSize = typeof ParagraphSizes[keyof typeof ParagraphSizes];

export const ParagraphFontWeights = {
  Regular: 'regular',
  Bold: 'bold'
} as const;
export type ParagraphFontWeight = typeof ParagraphFontWeights[keyof typeof ParagraphFontWeights];

export const paragraphSizeClassesMap = new Map<ParagraphSize, string>([
  [ParagraphSizes.XXS, `up-paragraph-${ParagraphSizes.XXS}`],
  [ParagraphSizes.XS, `up-paragraph-${ParagraphSizes.XS}`],
  [ParagraphSizes.S, `up-paragraph-${ParagraphSizes.S}`],
  [ParagraphSizes.Base, `up-paragraph-${ParagraphSizes.Base}`]
]);

export const paragraphFontWeightClassesMap = new Map<ParagraphFontWeight, string>([
  [ParagraphFontWeights.Regular, `up-paragraph-${ParagraphFontWeights.Regular}`],
  [ParagraphFontWeights.Bold, `up-paragraph-${ParagraphFontWeights.Bold}`]
]);

@Directive({
  selector: '[upParagraph]'
})
export class UpParagraphDirective
  extends UpTypographyBaseClass
  implements AfterViewInit, OnChanges
{
  @Input('upParagraph') size: ParagraphSize;
  @Input() fontWeight: ParagraphFontWeight = ParagraphFontWeights.Regular;

  readonly sizeClassesMap = paragraphSizeClassesMap;
  readonly fontWeightClassesMap = paragraphFontWeightClassesMap;
  readonly defaultSize = ParagraphSizes.Base;

  ngAfterViewInit(): void {
    super.ngAfterViewInit();

    if (!this.fontWeight) {
      this.renderer.addClass(
        this.hostElement.nativeElement,
        this.fontWeightClassesMap.get(this.fontWeight)
      );
      return;
    }

    if (!this.isFontWeightValid(this.fontWeight)) {
      console.error(`${this.fontWeight} is not a valid font weight.`);
      return;
    }
    this.renderer.addClass(
      this.hostElement.nativeElement,
      this.fontWeightClassesMap.get(this.fontWeight)
    );
  }

  ngOnChanges(changes: SimpleChanges): void {
    super.ngOnChanges(changes);

    if (changes.fontWeight) {
      this.renderer.removeClass(
        this.hostElement.nativeElement,
        this.fontWeightClassesMap.get(changes.fontWeight.previousValue)
      );
      this.renderer.addClass(
        this.hostElement.nativeElement,
        this.fontWeightClassesMap.get(changes.fontWeight.currentValue)
      );
    }
  }

  private isFontWeightValid(fontWeight: ParagraphFontWeight): boolean {
    return this.fontWeightClassesMap.has(fontWeight);
  }
}
