import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { Subject } from 'rxjs';
import { TableDisplayDensityService } from '../../services/table-display-density.service';
import { TableDisplayDensityEnum } from '../../enums';

@Component({
  selector: 'up-table-options',
  templateUrl: './table-options.component.html'
})
export class TableOptionsComponent implements OnInit {
  @Input() tableOptionsLabel = 'Table options';
  @Input() hasTableOptions = true;
  @Input() hasTableDisplayDensityOptions = true;
  @Output() openTableOptions: EventEmitter<void> = new EventEmitter();

  tableDisplayDensityEnum = TableDisplayDensityEnum;
  displayDensityMode$: Subject<TableDisplayDensityEnum>;

  constructor(
    @Inject(TableDisplayDensityService)
    private tableDisplayDensityService: TableDisplayDensityService
  ) {}

  ngOnInit(): void {
    this.displayDensityMode$ = this.tableDisplayDensityService.getDisplayDensity();
  }

  changeDisplayDensityMode(mode: TableDisplayDensityEnum) {
    this.tableDisplayDensityService.setDisplayDensity(mode);
  }
}
