import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  HostListener,
  Inject,
  Input,
  OnDestroy,
  OnInit,
  Output
} from '@angular/core';

import { notificationAnimation } from '../../animations';
import { PageNotificationState } from '../../enums';
import { PageNotification } from '../../interfaces';
import { WindowProperties, WindowProvider } from '../../providers';

@Component({
  selector: 'up-page-notification',
  exportAs: 'upPageNotification',
  templateUrl: 'page-notification.component.html',
  animations: [notificationAnimation]
})
export class PageNotificationComponent implements OnInit, OnDestroy {
  @Input()
  instance: PageNotification;

  @Output()
  readonly destroyed = new EventEmitter<{ id: string }>();

  private closeTimer?: number;
  private autoCloseTimer?: number;

  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    @Inject(WindowProvider) private window: WindowProperties
  ) {}

  ngOnInit(): void {
    if (this.autoCloseable) {
      this.startAutoCloseTimeout();
    }
  }

  ngOnDestroy(): void {
    if (this.autoCloseable) {
      this.clearAutoCloseTimer();
    }
  }

  close(event: MouseEvent): void {
    event.stopPropagation();
    this.destroy();
  }

  enter(): void {
    if (this.autoCloseable) {
      this.clearAutoCloseTimer();
    }
  }

  @HostListener('click', ['$event'])
  emitNotificationClick(event: MouseEvent): void {
    this.instance.onClick.next(event);
  }

  leave(): void {
    if (this.autoCloseable) {
      this.startAutoCloseTimeout();
    }
  }

  get autoCloseable(): boolean {
    return !!this.instance.duration;
  }

  get progressBarEnabled(): boolean {
    return this.autoCloseable && !!this.autoCloseTimer;
  }

  get progressBarDuration(): number {
    return this.progressBarEnabled ? this.instance.duration : 0;
  }

  private destroy(): void {
    this.instance.state = PageNotificationState.Leave;
    this.changeDetectorRef.detectChanges();
    this.closeTimer = this.window.setTimeout(this.closeTimerHandler, 200);
  }

  readonly closeTimerHandler = () => {
    this.closeTimer = undefined;
    this.destroyed.next({ id: this.instance.messageId });
  };

  private startAutoCloseTimeout(): void {
    this.autoCloseTimer = this.window.setTimeout(
      this.autoCloseTimerHandler,
      this.instance.duration
    );
  }

  readonly autoCloseTimerHandler = () => this.destroy();

  private clearAutoCloseTimer(): void {
    if (this.autoCloseTimer !== null) {
      this.window.clearTimeout(this.autoCloseTimer);
      this.autoCloseTimer = null;
    }
  }
}
