import { ChangeDetectionStrategy, Component, Inject, Input } from '@angular/core';
import {
  NotificationCategory,
  Notifications,
  NotificationsProvider,
  NotificationType
} from '../../notification.constants';

enum ProgressBarAnimationConfig {
  AnimationName = 'progress',
  FillMode = 'forwards'
}

@Component({
  selector: 'up-notification-progress-bar',
  templateUrl: './progress-bar.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class UpNotificationProgressBarComponent {
  @Input() duration = 3000;
  @Input() type: NotificationType = NotificationCategory.INFO;

  constructor(@Inject(NotificationsProvider) private provider: Notifications) {}

  get progressBarClassName(): string {
    return (
      this.provider.progressBarClassName.get(this.type) ||
      this.provider.progressBarClassName.get(NotificationCategory.INFO)
    );
  }

  get animationDuration(): string {
    return `${this.duration}ms`;
  }

  get animationCssProperty(): string {
    return `${ProgressBarAnimationConfig.AnimationName} ${this.animationDuration} ${ProgressBarAnimationConfig.FillMode}`;
  }

  get shouldAnimateProgressBar(): boolean {
    return this.duration > 0;
  }
}
