import { InjectionToken } from '@angular/core';

export const TabsPositionsTypes = {
  LEFT: 'left',
  RIGHT: 'right'
} as const;
export type TabPositionType = typeof TabsPositionsTypes[keyof typeof TabsPositionsTypes];

export const LevelTypes = {
  PAGE: 'Page',
  SECTION: 'Section'
} as const;
export type LevelType = typeof LevelTypes[keyof typeof LevelTypes];

export interface TabsLevelTypes {
  tabsLevelTypes: Map<LevelType, string>;
}

const tabsLevelTypes: Map<LevelType, string> = new Map<LevelType, string>();
tabsLevelTypes.set(LevelTypes.PAGE, 'Page');
tabsLevelTypes.set(LevelTypes.SECTION, 'Section');

export const TabsLevelTypesProvider = new InjectionToken<TabsLevelTypes>('tabsLevelTypesProvider');

export const TabsLevelFactory = () => ({
  tabsLevelTypes
});
