import {
  animate,
  AnimationTriggerMetadata,
  state,
  style,
  transition,
  trigger
} from '@angular/animations';

export const notificationAnimation: AnimationTriggerMetadata = trigger('notificationAnimation', [
  state('enter', style({ opacity: 1, transform: 'translateX(0)' })),
  transition('* => enter', [style({ transform: 'translateX(10%)' }), animate('100ms linear')]),
  state('leave', style({ opacity: 0, transform: 'scaleY(0.7)' })),
  transition('* => leave', [style({ opacity: 1, transform: 'scaleY(1)' }), animate('100ms linear')])
]);
