import { TemplateRef } from '@angular/core';
import { ComponentType, OverlayRef } from '@angular/cdk/overlay';
import { Subject } from 'rxjs';
import { KeyboardKeysEnum, SubscriptionManager } from '../../src/lib/utils';

import { UpModalConfig } from './modal.service';
import { UpModalComponent } from './modal.component';

export type ModalContentType<T> = TemplateRef<T> | ComponentType<T>;

export class ModalRef<T = unknown, D = unknown, R = unknown> extends SubscriptionManager {
  afterClosed$ = new Subject<R | undefined>();
  active = true;
  componentInstance: UpModalComponent<T, D, R>;

  constructor(
    public overlay: OverlayRef,
    public content: ModalContentType<T>,
    public data?: D,
    public config?: UpModalConfig
  ) {
    super();
    if (config.staticBackdrop) {
      return;
    }
    this.addSubscriptions(
      overlay.backdropClick().subscribe(() => this.close()),
      overlay.keydownEvents().subscribe((keydownEvent: KeyboardEvent) => {
        if (keydownEvent.key === KeyboardKeysEnum.Escape) {
          this.close();
        }
      })
    );
  }

  close = (result?: R): void => {
    this.active = false;
    this.overlay.dispose();
    this.afterClosed$.next(result);
    this.afterClosed$.complete();
    this.unsubscribeAll();
  };
}
