import { trigger, state, animate, transition, style } from '@angular/animations';

export const fade = trigger('fade', [
  state('true', style({ display: 'block', opacity: 0.5 })),
  state('false', style({ display: 'none', opacity: 0 })),
  transition('* => *', animate('.1s'))
]);

export const fadeInLeft = trigger('fadeInLeft', [
  state('true', style({ transform: 'translateZ(0)', display: 'block' })),
  state('false', style({ transform: 'translate3d(450px,0,0)', display: 'none' })),
  transition('* => *', animate('.2s'))
]);

export const fadeInRight = trigger('fadeInRight', [
  state('true', style({ transform: 'translateZ(0)', display: 'block' })),
  state('false', style({ transform: 'translate3d(-450px,0,0)', display: 'none' })),
  transition('* => *', animate('.2s'))
]);
