import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { fromEvent, Subscription } from 'rxjs';
import { tap, throttleTime } from 'rxjs/operators';

@Component({
  selector: 'up-text-ellipsis',
  templateUrl: './text-ellipsis.component.html',
  styleUrls: ['./text-ellipsis.component.sass']
})
export class UpTextEllipsisComponent implements OnInit, OnDestroy {
  @ViewChild('textOverflowContainer', { static: true }) textOverflowContainer: ElementRef;
  content: string;
  hasEllipsis = false;
  checkWindowResize: Subscription;

  ngOnInit() {
    this.checkIfHasEllipsis();
    this.checkWindowResize = fromEvent(window, 'resize')
      .pipe(throttleTime(60))
      .subscribe(() => {
        this.checkIfHasEllipsis();
      });
  }

  ngOnDestroy() {
    this.checkWindowResize.unsubscribe();
  }

  checkIfHasEllipsis() {
    requestAnimationFrame(() => {
      if (
        this.textOverflowContainer?.nativeElement?.offsetWidth <
        this.textOverflowContainer?.nativeElement?.scrollWidth
      ) {
        this.content = this.textOverflowContainer?.nativeElement?.innerText;
        return (this.hasEllipsis = true);
      }
      this.hasEllipsis = false;
    });
  }
}
