import { ControlValueAccessor } from '@angular/forms';
import { Position } from '../form-field-container/constants';
import { Input, Directive } from '@angular/core';
import { ParagraphSize, ParagraphSizes } from '../../typography/paragraph/paragraph.directive';

@Directive()
export class ToggleBaseComponent implements ControlValueAccessor {
  @Input() labelContainerClass: string;
  @Input() inputContainerClass: string;
  @Input() tooltip: string;
  @Input() persistTooltip = false;
  @Input() label: string;
  @Input() disabled: boolean;
  @Input() labelPosition: Position = Position.RIGHT;
  @Input() id = 'toggleId';
  @Input() dataTest: string;
  @Input() name: string;
  @Input() hasLabel = true;
  @Input() labelSize: ParagraphSize = ParagraphSizes.Base;
  @Input() value: boolean | string = true;

  @Input()
  toggled = false;

  onChange = (_) => {};
  onBlur = (_) => {};

  writeValue(obj: boolean | string): void {
    this.toggled = obj === this.value;
  }

  get hasLabelToLeft(): boolean {
    return this.hasLabel && this.labelPosition === Position.LEFT;
  }

  get hasLabelToRight(): boolean {
    return this.hasLabel && this.labelPosition === Position.RIGHT;
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onBlur = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  onChanged($event) {
    this.toggled = !!$event?.target?.checked;

    if (typeof this.value === 'boolean' && this.value === true) {
      this.onChange(this.toggled);
    } else {
      this.onChange(this.toggled ? this.value : '');
    }
  }
}
