import { Observable, Subscription } from 'rxjs';
import { UploadStats } from '../upload-status.constants';

export type UploadHandler = (file: File) => Observable<unknown>;
export type UploadSuccessHandler = (file: File, result: unknown) => void;
export type UploadErrorHandler = (file: File, error: unknown) => void;

export interface UploadItemHandlers {
  onClickHandler: Function;
  uploadCall: UploadHandler;
  onSuccess: UploadSuccessHandler;
  onFailed: UploadErrorHandler;
}

export interface UploadItemConfiguration {
  file: File;
  name: string;
  methods: UploadItemHandlers;
}

export class UploadPanelItem {
  name: string;
  progress?: number;
  file: File;
  status: UploadStats;
  methods: UploadItemHandlers;
  protected uploadSubscription$: Subscription;
  protected uploadResult: unknown;

  constructor(item: UploadItemConfiguration) {
    this.name = item.name;
    this.status = 'in_progress';
    this.progress = 0;
    this.file = item.file;
    this.methods = item.methods;
  }

  upload(): void {
    this.uploadSubscription$ = this.methods.uploadCall(this.file).subscribe(
      (result: unknown) => {
        this.status = 'completed';
        this.uploadResult = result;
        this.methods.onSuccess(this.file, result);
      },
      (error) => {
        this.status = 'failed';
        this.methods.onFailed(this.file, error);
      }
    );
  }

  cancel(): void {
    this.uploadSubscription$?.unsubscribe();
    this.status = 'cancelled';
    this.uploadSubscription$ = null;
  }

  retry(): void {
    this.status = 'in_progress';
    this.upload();
  }

  //@todo - this is not used atm in community/upload-panel-container-component - see https://tickets.unifiedpost.com/browse/COM-11655
  openItem(): void {
    this.methods.onClickHandler(this.uploadResult);
  }
}
