import { Component, ContentChildren, Inject, Input, QueryList, ViewEncapsulation } from '@angular/core';
import { UpTabComponent } from '../up-tab/tab.component';
import { LevelType, LevelTypes, TabsLevelTypes, TabsLevelTypesProvider } from './tabs.constants';
import { BadgeType } from '../../badge/enums';

@Component({
  selector: 'up-tabs',
  templateUrl: './tabs.component.html',
  styleUrls: ['./tabs.component.sass'],
  encapsulation: ViewEncapsulation.None
})
export class UpTabsComponent {
  @ContentChildren(UpTabComponent) tabs: QueryList<UpTabComponent>;
  @Input() borderEnabled = true;
  @Input() levelType: LevelType = LevelTypes.PAGE;

  constructor(@Inject(TabsLevelTypesProvider) private provider: TabsLevelTypes) {}

  selectTab(selectedTab: UpTabComponent): void {
    selectedTab.tabOpened.emit();
  }

  getBadgeStyle(tab: UpTabComponent): BadgeType {
    return tab.badgeStyle;
  }

  get levelTypeSection(): boolean {
    return this.provider.tabsLevelTypes.get(this.levelType) === LevelTypes.SECTION;
  }
}
