import {
  Component,
  HostBinding,
  HostListener,
  Input,
  OnChanges,
  OnInit,
  SimpleChanges
} from '@angular/core';
import { upIcon, upIconSpinnerMedium } from '../../icons/lib';

@Component({
  selector: 'up-action-bar-action',
  templateUrl: './action-bar-action.component.html'
})
export class UpActionBarActionComponent implements OnChanges, OnInit {
  iconColor = 'white';
  actionPrefixIcon: upIcon;
  hasPrefixIcon: boolean;

  @Input() prefixIcon?: upIcon;
  @Input() suffixIcon?: upIcon;

  @HostBinding('class.action-highlighted')
  @Input()
  highlighted = false;

  @HostBinding('class.action-loading')
  @Input()
  loading = false;

  @HostBinding('class.action-disabled')
  @Input()
  disabled = false;

  @HostBinding('class.up-action-bar-action') containerClass = true;

  @HostListener('mouseover') onHover() {
    if (this.loading) {
      return;
    }

    this.iconColor = 'var(--up-brand-1-200)';
  }

  @HostListener('mouseleave') onMouseOut() {
    this.iconColor = 'white';
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.loading) {
      this.setActionPrefixIcon();
      this.setHasPrefixIcon();
    }

    if (changes.prefixIcon) {
      this.setHasPrefixIcon();
    }
  }

  ngOnInit() {
    this.setActionPrefixIcon();
    this.setHasPrefixIcon();
  }

  private setHasPrefixIcon() {
    this.hasPrefixIcon = !!this.prefixIcon || this.loading;
  }

  private setActionPrefixIcon() {
    this.actionPrefixIcon = this.loading ? upIconSpinnerMedium.name : this.prefixIcon;
  }
}
