import { Component, Inject } from '@angular/core';
import { ModalRef } from '../modal-ref';

export interface ConfirmModalConfig {
  confirmLabel: string;
  cancelLabel: string;
  title?: string;
  body?: string;
  confirmable?: boolean;
  cancelable?: boolean;
  hideModalActions?: boolean;
}

@Component({
  selector: 'up-confirm-modal',
  templateUrl: './confirm-modal.component.html'
})
export class UpConfirmModalComponent {
  constructor(
    @Inject(ModalRef)
    private modalRef: ModalRef<UpConfirmModalComponent, ConfirmModalConfig, boolean>
  ) {}

  get data(): ConfirmModalConfig {
    const modalRefData: Partial<ConfirmModalConfig> = this.modalRef.data || {};
    const defaults = {
      confirmable: true,
      cancelable: true,
      hideModalActions: false,
      title: '',
      body: '',
      confirmLabel: '',
      cancelLabel: ''
    };
    return { ...defaults, ...modalRefData };
  }

  confirm() {
    this.modalRef.close(true);
  }

  cancel() {
    this.modalRef.close(false);
  }
}
