import { Overlay } from '@angular/cdk/overlay';
import { ComponentPortal } from '@angular/cdk/portal';
import { Injectable, Injector } from '@angular/core';

import { PageNotificationContainerComponent } from './components';
import { PageNotification, CreateNotificationOptions } from './interfaces';

const OVERLAY_CONTAINER_Z_INDEX = '1010';

@Injectable()
export class UpPageNotificationService {
  private containerInstance: PageNotificationContainerComponent | undefined;

  constructor(private overlay: Overlay, private injector: Injector) {}

  get container(): PageNotificationContainerComponent {
    return this.containerInstance;
  }

  private createContainer(): PageNotificationContainerComponent {
    const overlayRef = this.overlay.create({
      hasBackdrop: false,
      scrollStrategy: this.overlay.scrollStrategies.noop(),
      positionStrategy: this.overlay.position().global()
    });
    const componentPortal = new ComponentPortal(
      PageNotificationContainerComponent,
      null,
      this.injector
    );
    const componentRef = overlayRef.attach(componentPortal);
    overlayRef.overlayElement.style.zIndex = OVERLAY_CONTAINER_Z_INDEX;

    return componentRef.instance;
  }

  add(options: CreateNotificationOptions): PageNotification {
    if (!this.container) {
      this.containerInstance = this.createContainer();
    }
    return this.container.create(options);
  }

  remove(id?: string): void {
    if (!this.container) {
      return;
    }
    if (id) {
      this.container.remove(id);
    } else {
      this.container.removeAll();
    }
  }
}
