import { InjectionToken } from '@angular/core';

export type UploadStats = 'in_progress' | 'completed' | 'failed' | 'cancelled';

export interface UploadStatus {
  cssClassesBasedOnStatus: Map<UploadStats, string>;
  fileNameCssClassesBasedOnStatus: Map<UploadStats, string>;
}

export const UploadStatusProvider = new InjectionToken<UploadStatus>('uploadStatusProvider');

const cssClassesBasedOnStatus: Map<UploadStats, string> = new Map<UploadStats, string>();
const fileNameCssClassesBasedOnStatus: Map<UploadStats, string> = new Map<UploadStats, string>();

cssClassesBasedOnStatus.set('in_progress', 'up-upload-item-in-progress');
cssClassesBasedOnStatus.set('completed', 'up-upload-item');
cssClassesBasedOnStatus.set('failed', 'up-upload-item-failure');
cssClassesBasedOnStatus.set('cancelled', 'up-upload-item-cancelled');

fileNameCssClassesBasedOnStatus.set('in_progress', 'upload-in-progress');
fileNameCssClassesBasedOnStatus.set('completed', 'upload-completed');
fileNameCssClassesBasedOnStatus.set('cancelled', 'upload-cancelled');

export const UploadStatusFactory = () => ({
  cssClassesBasedOnStatus,
  fileNameCssClassesBasedOnStatus
});
