import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'up-header-navigation',
  templateUrl: './header-navigation.component.html',
  styleUrls: ['./header-navigation.component.sass']
})
export class HeaderNavigationComponent {
  @Input()
  previousOptionText = 'Previous';

  @Input()
  nextOptionText = 'Next';

  @Input()
  previousOptionDisabled = false;

  @Input()
  nextOptionDisabled = false;

  @Output()
  previousPage: EventEmitter<void> = new EventEmitter<void>();

  @Output()
  nextPage: EventEmitter<void> = new EventEmitter<void>();

  get previousOptionColor(): string {
    return this.previousOptionDisabled ? 'var(--up-neutral-700)' : 'var(--up-brand-1-500)';
  }

  get nextOptionColor(): string {
    return this.nextOptionDisabled ? 'var(--up-neutral-700)' : 'var(--up-brand-1-500)';
  }
}
