import { InjectionToken } from '@angular/core';
import { ConnectedPosition } from '@angular/cdk/overlay';

export const TooltipTriggerType = {
  HOVER: 'hover',
  CLICK: 'click'
} as const;
export type TooltipTrigger = typeof TooltipTriggerType[keyof typeof TooltipTriggerType];

export const TooltipPositionType = {
  RIGHT: 'right',
  LEFT: 'left',
  BOTTOM: 'bottom',
  TOP: 'top'
} as const;
export type TooltipPosition = typeof TooltipPositionType[keyof typeof TooltipPositionType];

export interface TooltipConstants {
  positions: Map<string, ConnectedPosition>;
  hoverOffPersistTime: number;
}

export const TooltipConstantsProvider = new InjectionToken<TooltipConstants>(
  'tooltipConstantsProvider'
);

const positions: Map<string, ConnectedPosition> = new Map<string, ConnectedPosition>();

positions.set(TooltipPositionType.RIGHT, {
  originX: 'end',
  originY: 'center',
  overlayX: 'start',
  overlayY: 'center'
});

positions.set(TooltipPositionType.LEFT, {
  originX: 'start',
  originY: 'center',
  overlayX: 'end',
  overlayY: 'center'
});

positions.set(TooltipPositionType.BOTTOM, {
  originX: 'center',
  originY: 'bottom',
  overlayX: 'center',
  overlayY: 'top'
});

positions.set(TooltipPositionType.TOP, {
  originX: 'center',
  originY: 'top',
  overlayX: 'center',
  overlayY: 'bottom'
});

export const TooltipConstantsFactory = () => ({
  positions,
  hoverOffPersistTime: 1000
});
