import {
  Directive,
  Input,
  ElementRef,
  Inject,
  Renderer2,
  AfterViewInit,
  OnChanges,
  SimpleChanges
} from '@angular/core';
import {
  ButtonClassesProvider,
  ButtonClasses,
  ButtonType,
  ButtonWidth,
  ButtonTypes,
  ButtonColors,
  ButtonColor
} from './button.constants';
import { ButtonCSSClasses } from './enums/button-css-class.enum';
import { ButtonWidthCSSClasses } from './enums/button-width-css-class.enum';
import { AppTheme, AppThemes } from '../../src/lib/theme/types/types';

@Directive({
  selector: '[upButton]'
})
export class UpButtonDirective implements AfterViewInit, OnChanges {
  @Input() upButton: ButtonType;
  @Input() width: ButtonWidth;
  @Input() theme: AppTheme = AppThemes.LIGHT;
  @Input() color: ButtonColor = ButtonColors.DEFAULT;

  constructor(
    @Inject(ElementRef) protected el: ElementRef,
    @Inject(Renderer2) protected renderer: Renderer2,
    @Inject(ButtonClassesProvider) protected provider: ButtonClasses
  ) {}

  ngAfterViewInit(): void {
    this.addClass(ButtonCSSClasses.BASE);
    this.addClass(this.typeClass);
    this.addClass(this.buttonWidthClass);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.upButton || changes.theme || changes.color) {
      this.removeAllTypeClasses();
      this.addCSSClasses();
    }

    if (changes.width) {
      this.removeAllWidthClasses();
      this.addClass(ButtonCSSClasses.BASE);
      this.addClass(this.buttonWidthClass);
    }
  }

  get typeClass(): ButtonCSSClasses {
    return (
      this.provider.buttonClasses.get(this.upButton) ||
      this.provider.buttonClasses.get(ButtonTypes.PRIMARY)
    );
  }

  get buttonWidthClass(): ButtonWidthCSSClasses {
    return this.provider.buttonWidthClasses.get(this.width);
  }

  private addCSSClasses(): void {
    this.addClass(this.typeClass);

    if (this.theme === AppThemes.DARK) {
      this.addClass(ButtonCSSClasses.DARK_THEME);
    }
    if (this.color === ButtonColors.DANGER) {
      switch (this.upButton) {
        case ButtonTypes.PRIMARY:
          this.addClass(ButtonCSSClasses.PRIMARY_DANGER);
          break;
        case ButtonTypes.SECONDARY:
          this.addClass(ButtonCSSClasses.SECONDARY_DANGER);
          break;
        case ButtonTypes.SECONDARY_OUTLINE:
          this.addClass(ButtonCSSClasses.SECONDARY_OUTLINE_DANGER);
          break;
        case ButtonTypes.TERTIARY:
          this.addClass(ButtonCSSClasses.TERTIARY_DANGER);
          break;
        case ButtonTypes.ROUNDED:
          this.addClass(ButtonCSSClasses.ROUNDED_DANGER);
          break;
      }
    }
  }

  protected addClass(buttonClass: string): void {
    if (buttonClass) {
      this.renderer.addClass(this.el.nativeElement, buttonClass);
    }
  }

  private removeAllTypeClasses(): void {
    Object.values(ButtonCSSClasses)
      .filter((cls) => cls !== 'up-button')
      .forEach((buttonClass) => {
        this.renderer.removeClass(this.el.nativeElement, buttonClass);
      });
  }

  private removeAllWidthClasses(): void {
    this.provider.buttonWidthClasses.forEach((buttonWidthClass) =>
      this.renderer.removeClass(this.el.nativeElement, buttonWidthClass)
    );
  }
}
