import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Inject } from '@angular/core';
import { Subject } from 'rxjs';

import {
  CreateNotificationOptions,
  PageNotification,
  PageNotificationConfig
} from '../../interfaces';
import { PageNotificationConfigProvider } from '../../providers';
import { PageNotificationState } from '../../enums';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  selector: 'up-page-notification-container',
  templateUrl: 'page-notification-container.component.html'
})
export class PageNotificationContainerComponent {
  private notificationId = 0;
  instances: PageNotification[] = [];

  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    @Inject(PageNotificationConfigProvider) private notificationConfig: PageNotificationConfig
  ) {
    this.changeDetectorRef.markForCheck();
  }

  static instanceRemoved(instance: PageNotification): void {
    instance.onClose.next(null);
    instance.onClose.complete();
  }

  create(options: CreateNotificationOptions): PageNotification {
    const { duration } = this.notificationConfig;

    const notification: PageNotification = {
      type: options.type,
      title: options.title || '',
      content: options.content || '',
      state: PageNotificationState.Enter,
      duration: options.duration || duration,
      footerTemplate: options.footerTemplate,
      messageId: this.generateNotificationId(),
      createdAt: new Date(),
      onClose: new Subject<boolean>(),
      onClick: new Subject<MouseEvent>()
    };

    if (this.instances.length >= this.notificationConfig.maxStack) {
      this.instances = this.instances.slice(1);
    }

    this.instances = [...this.instances, notification];

    this.instancesChanged();

    return notification;
  }

  remove(id: string): void {
    const instance = this.instances.find((item) => item.messageId === id);
    if (!instance) {
      return;
    }
    this.instances = this.instances.filter((item) => item !== instance);
    PageNotificationContainerComponent.instanceRemoved(instance);
    this.instancesChanged();
  }

  removeAll(): void {
    this.instances.forEach((instance) =>
      PageNotificationContainerComponent.instanceRemoved(instance)
    );
    this.instances = [];
    this.instancesChanged();
  }

  private instancesChanged(): void {
    this.changeDetectorRef.detectChanges();
  }

  private generateNotificationId(): string {
    return `notification-${this.notificationId++}`;
  }
}
