import { ElementRef, Inject, OnInit, Renderer2 } from '@angular/core';
import { Directive, Input } from '@angular/core';
import {
  SpacingClassSuffix,
  SpacingCSSClassPrefix,
  SpacingMultiplier,
  SpacingMultiplierAliases
} from './spacing.constants';

@Directive()
export abstract class SpacingBaseDirective implements OnInit {
  abstract prefix: string;

  abstract multiplier: SpacingMultiplier;

  constructor(
    @Inject(ElementRef) protected el: ElementRef,
    @Inject(Renderer2) protected renderer: Renderer2
  ) {}

  ngOnInit(): void {
    this.renderer.addClass(this.el.nativeElement, `${this.prefix}-${this.suffix}`);
  }

  get suffix(): SpacingClassSuffix {
    const alias = SpacingMultiplierAliases[this.multiplier];
    if (!this.multiplier) {
      return '0';
    }
    return !!alias ? alias : this.multiplier;
  }
}

@Directive({ selector: '[upMargin]' })
export class UpMarginDirective extends SpacingBaseDirective {
  @Input('upMargin') multiplier: SpacingMultiplier;
  prefix = SpacingCSSClassPrefix.Margin;
}

