import {
  ChangeDetectionStrategy,
  Component, EventEmitter,
  HostListener,
  Input, Output
} from '@angular/core';
import {
  fade,
  fadeInLeft,
  fadeInRight
} from './animations';

export enum DrawerLocation {
  Left = 'left',
  Right = 'right'
}

@Component({
  selector: 'up-drawer',
  templateUrl: './drawer.component.html',
  styleUrls: ['./drawer.component.sass'],
  animations: [fade, fadeInLeft, fadeInRight],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DrawerComponent {
  @Input() opened = false;
  @Input() showCloseButton = false;
  @Input() location: DrawerLocation.Left | DrawerLocation.Right = DrawerLocation.Right;

  @Output() closed: EventEmitter<void> = new EventEmitter<void>();

  constructor() {
  }

  get isLeftDrawer(): boolean {
    return this.location === DrawerLocation.Left;
  }

  @HostListener('document:keydown.esc', ['$event'])
  handleEscape(event): void {
    event.preventDefault();
    this.hideDrawer();
  }

  hideDrawer(): void {
    this.closed.emit();
  }
}
