import { InjectionToken } from '@angular/core';

export const SumCategory = {
  TOTAL: 'total',
  CRITICAL: 'overdue',
  WARNING: 'warning',
  DEFAULT: 'default'
} as const;

export type SumType = typeof SumCategory[keyof typeof SumCategory];

export interface SumTypes {
  types: Map<SumType, string>;
}

export const SumsTypeProvider = new InjectionToken<SumTypes>('sumsTypeProvider');

const types: Map<SumType, string> = new Map<SumType, string>();
types.set(SumCategory.TOTAL, 'up-sums-component-total');
types.set(SumCategory.CRITICAL, 'up-sums-component-critical');
types.set(SumCategory.WARNING, 'up-sums-component-warning');
types.set(SumCategory.DEFAULT, 'up-sums-component-default');

export const SumsTypeFactory = () => ({
  types
});

export const SumLabelSizes = {
  XXS: 'XXS',
  XS: 'XS',
  S: 'S',
  Base: 'base',
  L: 'L',
  XL: 'XL',
  XXL: 'XXL',
  XXXL: 'XXXL'
} as const;

export const SumsSizeProvider = new InjectionToken<SumLabelSizes>('sumsSizeProvider');

export type SumLabelSizes = typeof SumLabelSizes[keyof typeof SumLabelSizes];

export const sumLabelSizeClassesMap = new Map<SumLabelSizes, string>([
  [SumLabelSizes.XXS, `up-sum-label-${SumLabelSizes.XXS}`],
  [SumLabelSizes.XS, `up-sum-label-${SumLabelSizes.XS}`],
  [SumLabelSizes.S, `up-sum-label-${SumLabelSizes.S}`],
  [SumLabelSizes.Base, `up-sum-label-${SumLabelSizes.Base}`],
  [SumLabelSizes.L, `up-sum-label-${SumLabelSizes.L}`],
  [SumLabelSizes.XL, `up-sum-label-${SumLabelSizes.XL}`],
  [SumLabelSizes.XXL, `up-sum-label-${SumLabelSizes.XXL}`],
  [SumLabelSizes.XXXL, `up-sum-label-${SumLabelSizes.XXXL}`]
]);

export const SumsSizeFactory = () => ({
  sumLabelSizeClassesMap
});
