import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { PDFDocumentProxy } from 'ng2-pdf-viewer';
import { PdfPreviewZoomScale, PdfPreviewZoomScaleType } from '../constants/pdf-preview.constants';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { PercentageInputMaskDirective } from '../../../forms/percentage-input-mask.directive';
import { PdfPreviewMagnifyingGlassComponent } from '../pdf-preview-magnifying-glass/pdf-preview-magnifying-glass.component';
import { PdfPreviewCopyToClipboardComponent } from '../pdf-preview-copy-to-clipboard/pdf-preview-copy-to-clipboard.component';
import { PdfPreviewSignPlaceholderComponent } from '../pdf-preview-sign-placeholder/pdf-preview-sign-placeholder.component';

@Component({
  selector: 'up-pdf-preview',
  templateUrl: './pdf-preview.component.html',
  styleUrls: ['./pdf-preview.component.sass'],
  encapsulation: ViewEncapsulation.None
})
export class PdfPreviewComponent implements OnInit, OnChanges {
  @ViewChild(PercentageInputMaskDirective) zoomPercentageMask: PercentageInputMaskDirective;
  @ViewChild(PdfPreviewMagnifyingGlassComponent)
  pdfPreviewMagnifyingGlassComponent: PdfPreviewMagnifyingGlassComponent;

  @ViewChild(PdfPreviewCopyToClipboardComponent)
  pdfPreviewCopyToClipboardComponent: PdfPreviewCopyToClipboardComponent;

  @ViewChild(PdfPreviewSignPlaceholderComponent) pdfPreviewSignPlaceholderComponent: PdfPreviewSignPlaceholderComponent

  @Input() pdfSrc: string | { url: string; httpHeaders?: unknown; withCredentials?: boolean } = '';
  @Input() downloadUrl = '';
  @Input() fullScreenUrl = '';
  @Input() defaultZoom = 1;

  @Input() fitToPageHeightLabel = 'Fit to height';
  @Input() fitToPageWidthLabel = 'Fit to Width';
  @Input() fitToEntirePageLabel = 'Fit to entire page';
  @Input() downloadButtonLabel = 'Download PDF';
  @Input() fullScreenButtonLabel = 'View in full screen';
  @Input() pageLabel = 'Page';
  @Input() errorMessage = 'Pdf preview is not available';

  @Input() magnifyingGlassTooltip =
    'Using the Magnifying glass will no longer allow text selection in the PDF and will disable the zooming in/out';
  @Input() magnifyingGlassEnabledTooltip =
    'To enable text selection, zooming in/out and fit to page features, turn off the magnifying glass.';
  @Input() magnifyingGlassEnabled = false;
  @Input() magnifyingGlassDefaultState = false;
  @Input() magnifyingGlassMouseSensibility = 10;
  @Input() magnifyingSize: string | number = 300;

  @Input() placeHolderSize: { height: number | string; width: number | string } = { height: 50, width: 100 };
  @Input() signPlaceholderMouseSensibility = 10;
  @Input() enableSignPlaceholder = false;
  @Input() labelSignaturePlaceholder = 'Signature';
  @Output() signPlaceholderCoordinates: EventEmitter<any> = new EventEmitter<any>();

  @Input() automaticallyCopySelection = false;
  @Input() copiedLabel = 'Copied';
  @Input() automaticallyCopySelectionDelay = 500;
  @Input() automaticallyCopySelectionTooltipCloseDelay = 1000;

  @Input() disablePdfOptions = false;

  @Output() pdfLoaded: EventEmitter<PDFDocumentProxy> = new EventEmitter<PDFDocumentProxy>();
  @Output() pdfError: EventEmitter<unknown> = new EventEmitter<unknown>();
  @Output() magnifyingGlassVisibilityChanged: EventEmitter<boolean> = new EventEmitter<boolean>();

  showAllPages = true;
  isMagnifyingGlassVisible = false;
  isSignPlaceholderVisible = false;
  zoomScale: PdfPreviewZoomScaleType = PdfPreviewZoomScale.PAGE_FIT;
  page = 1;
  zoom = this.defaultZoom;
  totalPages;

  zoomScaleTypes = PdfPreviewZoomScale;
  isZoomControlDropdownOpen = false;
  pdfPreviewHasErrors = false;

  form: UntypedFormGroup = new UntypedFormGroup({
    zoomPercentage: new UntypedFormControl(100),
    page: new UntypedFormControl(1)
  });

  document: PDFDocumentProxy;

  constructor(public element: ElementRef) {}

  get magnifyingGlassButtonColor() {
    return !this.document ? 'var(--up-neutral-500)' : 'var(--up-brand-1-700)';
  }

  get prevPageButtonColor() {
    return this.page === 1 ? 'var(--up-neutral-500)' : 'var(--up-brand-1-700)';
  }

  get nextPageButtonColor() {
    return this.page === this.totalPages ? 'var(--up-neutral-500)' : 'var(--up-brand-1-700)';
  }

  get zoomOutButtonColor() {
    return this.zoom < 0.11 || this.isMagnifyingGlassVisible || this.isSignPlaceholderVisible
      ? 'var(--up-neutral-500)'
      : 'var(--up-brand-1-700)';
  }

  get controlColor() {
    return this.isMagnifyingGlassVisible || this.enableSignPlaceholder ? 'var(--up-neutral-500)' : 'var(--up-brand-1-700)';
  }

  ngOnInit(): void {
    this.resetZoom();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.pdfSrc) {
      this.pdfPreviewHasErrors = false;
    }
  }

  setCoordinates(event) {
    if(this.enableSignPlaceholder) {
      this.signPlaceholderCoordinates.emit(event)
    }
  }

  afterLoadComplete(pdf: PDFDocumentProxy) {
    this.pdfPreviewHasErrors = false;
    this.totalPages = pdf.numPages;
    this.pdfLoaded.next(pdf);
    this.document = pdf;

    if (this.automaticallyCopySelection) {
      setTimeout(() => {
        this.pdfPreviewCopyToClipboardComponent.activateSelectionCopy();
      });
    }

    if (this.magnifyingGlassDefaultState && this.magnifyingGlassEnabled) {
      setTimeout(() => {
        this.activateMagnifyingGlass();
      });
    }

    if (this.enableSignPlaceholder) {
      setTimeout(() => {
        this.activateSignPlaceholder();
      })
    }
  }

  activateSignPlaceholder() {
    this.form.controls.zoomPercentage.disable();
    this.pageFit();
    this.showAllPages = false;
    this.page = 1;
    this.zoom = 1;
    this.form.controls.zoomPercentage.setValue(Math.floor(this.zoom * 100));
    this.pdfPreviewSignPlaceholderComponent.createSignPlaceholder();
    this.zoomPercentageMask?.inputBlur(this.form.controls.zoomPercentage.value);
    this.isSignPlaceholderVisible = true;
  }

  activateMagnifyingGlass() {
    this.form.controls.zoomPercentage.disable();
    this.pageFit();
    this.showAllPages = false;
    this.page = 1;
    this.zoom = 1;
    this.form.controls.zoomPercentage.setValue(Math.floor(this.zoom * 100));
    this.zoomPercentageMask?.inputBlur(this.form.controls.zoomPercentage.value);
    this.pdfPreviewMagnifyingGlassComponent.createMagnifyingGlass();
    this.isMagnifyingGlassVisible = true;
    this.magnifyingGlassVisibilityChanged.emit(true);
  }

  deactivateMagnifyingGlass() {
    this.form.controls.zoomPercentage.enable();
    this.showAllPages = true;
    this.pdfPreviewMagnifyingGlassComponent.destroyMagnifyingGlass();
    this.isMagnifyingGlassVisible = false;
    this.page = 1;
    this.magnifyingGlassVisibilityChanged.emit(false);
  }

  onLoadError(error: unknown) {
    this.pdfError.next(error);
    this.pdfPreviewHasErrors = true;
  }

  onZoomControlsDropdownOpen() {
    this.isZoomControlDropdownOpen = true;
  }

  onZoomControlsDropdownClose() {
    this.isZoomControlDropdownOpen = false;
  }

  pageFit() {
    this.resetZoom();
    this.zoomScale = PdfPreviewZoomScale.PAGE_FIT;
  }

  pageWidth() {
    this.resetZoom();
    this.zoomScale = PdfPreviewZoomScale.PAGE_WIDTH;
  }

  pageHeight() {
    this.resetZoom();
    this.zoomScale = PdfPreviewZoomScale.PAGE_HEIGHT;
  }

  nextPage() {
    if (this.page === this.totalPages) {
      return;
    }

    this.page = this.page + 1;
    this.form.controls.page.setValue(this.page);
  }

  prevPage() {
    if (this.page === 1) {
      return;
    }

    this.page = this.page - 1;
    this.form.controls.page.setValue(this.page);
  }

  zoomIn() {
    if (this.isMagnifyingGlassVisible || this.isSignPlaceholderVisible) {
      return;
    }

    this.zoom = this.zoom + 0.1;
    this.form.controls.zoomPercentage.setValue(Math.floor(this.zoom * 100));
    this.zoomPercentageMask?.inputBlur(this.form.controls.zoomPercentage.value);
  }

  zoomOut() {
    if (this.isMagnifyingGlassVisible || this.zoom < 0.11 || this.isSignPlaceholderVisible) {
      return;
    }

    this.zoom = this.zoom - 0.1;
    this.form.controls.zoomPercentage.setValue(Math.floor(this.zoom * 100));
    this.zoomPercentageMask?.inputBlur(this.form.controls.zoomPercentage.value);
  }

  zoomPercentageChanged() {
    this.zoom = this.form.controls.zoomPercentage.value / 100;
  }

  pageChanged() {
    this.page = this.form.controls.page.value;
  }

  private resetZoom() {
    this.zoom = this.defaultZoom;
    this.form.controls.zoomPercentage.setValue(Math.floor(this.zoom * 100));
    this.zoomPercentageMask?.inputBlur(this.form.controls.zoomPercentage.value);
  }
}
