import {
  Component,
  ContentChild,
  Directive,
  EventEmitter,
  Input,
  Output,
  TemplateRef
} from '@angular/core';
import { TabPositionType, TabsPositionsTypes } from '../up-tabs-component/tabs.constants';
import { BadgeType, BadgeTypes } from '../../badge/enums';

@Directive({
  selector: '[upTabTitle]'
})
export class TabTitleDirective {
  constructor(public template: TemplateRef<unknown>) {}
}

@Component({
  selector: 'up-tab',
  templateUrl: './tab.component.html'
})
export class UpTabComponent {
  @ContentChild(TabTitleDirective, { read: TemplateRef }) titleTemplate: TemplateRef<unknown>;

  @Input() title: string;
  @Input() count: number;
  @Input() active = false;
  @Input() disabled = false;
  @Input() grayedOut = false;
  @Input() separatorVisible: boolean;
  @Input() badgeStyle: BadgeType = BadgeTypes.TABS_WHITE;
  @Input() position: TabPositionType = TabsPositionsTypes.LEFT;
  @Input() dataTest: string;

  @Output() tabOpened: EventEmitter<void> = new EventEmitter();

  hasCounter() {
    return !isNaN(parseInt(String(this.count), 10));
  }
}
