import {
  ComponentType,
  GlobalPositionStrategy,
  Overlay,
  OverlayConfig
} from '@angular/cdk/overlay';
import {
  ComponentFactoryResolver,
  ElementRef,
  Injectable,
  Injector,
  TemplateRef,
  ViewContainerRef
} from '@angular/core';
import { ComponentPortal, TemplatePortal } from '@angular/cdk/portal';

import { ModalRef } from './modal-ref';
import { ModalSize, UpModalComponent } from './modal.component';

enum ModalCssClasses {
  PanelClass = 'up-modal-panel',
  BackdropClass = 'up-modal-backdrop'
}

export interface UpModalConfig {
  id?: number;
  viewContainerRef?: ViewContainerRef;
  originElementRef?: ElementRef;
  modalCSSClass?: string;
  staticBackdrop?: boolean;
  size?: ModalSize;
  componentFactoryResolver?: ComponentFactoryResolver;
}

@Injectable()
export class UpModalService {
  overlayConfig: OverlayConfig;
  modals: ModalRef[] = [];

  constructor(private overlay: Overlay, private injector: Injector) {
    this.overlayConfig = new OverlayConfig({
      hasBackdrop: true,
      panelClass: ModalCssClasses.PanelClass,
      backdropClass: ModalCssClasses.BackdropClass,
      positionStrategy: new GlobalPositionStrategy()
    });
  }

  open<T, D, R>(
    content: ComponentType<T> | TemplateRef<T>,
    data?: D,
    config: UpModalConfig = {}
  ): ModalRef<T, D, R> {
    const overlay = this.overlay.create(this.overlayConfig);
    const modalRef = new ModalRef<T, D, R>(overlay, content, data, config);
    const userInjector = config && config.viewContainerRef && config.viewContainerRef.injector;
    const injector = Injector.create({
      parent: userInjector || this.injector,
      providers: [{ provide: ModalRef, useValue: modalRef }]
    });
    const viewContainerRef = config.viewContainerRef || null;

    const overlayRef = overlay.attach(
      new ComponentPortal(
        UpModalComponent,
        viewContainerRef,
        injector,
        config.componentFactoryResolver
      )
    );

    if (content instanceof TemplateRef) {
      overlayRef.instance.attachTemplatePortal(
        // @ts-ignore
        new TemplatePortal(content, viewContainerRef, modalRef)
      );
    } else {
      const componentRef = overlayRef.instance.attachComponentPortal(
        new ComponentPortal(
          content,
          config.viewContainerRef,
          Injector.create({
            parent: userInjector || this.injector,
            providers: [{ provide: ModalRef, useValue: modalRef }]
          }),
          config.componentFactoryResolver
        )
      );
      modalRef.componentInstance = componentRef.instance as UpModalComponent<T, D, R>;
    }

    this.modals.push(modalRef);

    return modalRef;
  }

  closeAll(): void {
    this.modals.filter((modal) => modal.active).forEach((modal) => modal.close());
  }
}
