import { Component, Input, Output, EventEmitter, Inject } from '@angular/core';
import { UploadStats, UploadStatusProvider, UploadStatus } from '../../upload-status.constants';

@Component({
  selector: 'up-upload-item',
  templateUrl: 'upload-item.component.html',
  styleUrls: ['upload-item.component.sass']
})
export class UploadItemComponent {
  cancelIconColor = 'var(--up-neutral-400)';

  @Input()
  fileName: string;

  @Input()
  uploadStatus: UploadStats;

  @Input()
  uploadProgress?: number;

  @Input()
  uploadCancelledMessage: string;

  @Input()
  uploadFailureMessage: string;

  @Output()
  navigateToDocumentDetails: EventEmitter<string> = new EventEmitter<string>();

  @Output()
  cancelUpload: EventEmitter<string> = new EventEmitter<string>();

  @Output()
  retryUpload: EventEmitter<string> = new EventEmitter<string>();

  constructor(@Inject(UploadStatusProvider) private provider: UploadStatus) {}

  get isUploading(): boolean {
    return this.uploadStatus === 'in_progress';
  }

  get isUploadCompleted(): boolean {
    return this.uploadStatus === 'completed';
  }

  get isUploadFailed(): boolean {
    return this.uploadStatus === 'failed';
  }

  get isUploadCancelled(): boolean {
    return this.uploadStatus === 'cancelled';
  }

  get isUploadProgressProvided(): boolean {
    return this.uploadProgress !== undefined;
  }

  get uploadItemCSSClass(): string {
    return this.provider.cssClassesBasedOnStatus.get(this.uploadStatus);
  }

  get filenameCSSClass(): string {
    return this.provider.fileNameCssClassesBasedOnStatus.get(this.uploadStatus);
  }

  get cancelUploadIconColor(): string {
    return this.cancelIconColor;
  }

  //@todo - this is not used anywhere atm - see https://tickets.unifiedpost.com/browse/COM-11655
  handleNavigateToDocumentDetails(): void {
    this.navigateToDocumentDetails.emit(this.fileName);
  }

  handleCancelItemUpload(): void {
    this.cancelUpload.emit(this.fileName);
  }

  handleRetryUpload(): void {
    this.retryUpload.emit(this.fileName);
  }

  toggleCancelUploadIconColor($event: { type: string }): void {
    this.cancelIconColor =
      $event.type == 'mouseover' ? 'var(--up-brand-1-800)' : 'var(--up-neutral-400)';
  }
}
