import { ConnectionPositionPair } from '@angular/cdk/overlay';

export enum popOutPositions {
  TopLeft = 'topLeft',
  TopRight = 'topRight',
  BottomLeft = 'bottomLeft',
  BottomRight = 'bottomRight'
}

export const dropdownPositionsMap = new Map<popOutPositions, ConnectionPositionPair>([
  [
    popOutPositions.TopLeft,
    {
      originX: 'start',
      originY: 'top',
      overlayX: 'start',
      overlayY: 'bottom'
    }
  ],
  [
    popOutPositions.TopRight,
    {
      originX: 'end',
      originY: 'top',
      overlayX: 'end',
      overlayY: 'bottom'
    }
  ],
  [
    popOutPositions.BottomLeft,
    {
      originX: 'start',
      originY: 'bottom',
      overlayX: 'start',
      overlayY: 'top'
    }
  ],
  [
    popOutPositions.BottomRight,
    {
      originX: 'end',
      originY: 'bottom',
      overlayX: 'end',
      overlayY: 'top'
    }
  ]
]);
