import {
  Component,
  ContentChildren,
  forwardRef,
  HostBinding,
  Input,
  QueryList,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { UpCheckboxListItemComponent } from './up-checkbox-list-item/up-checkbox-list-item.component';

@Component({
  selector: 'up-checkbox-list',
  templateUrl: './up-checkbox-list.component.html',
  styleUrls: ['./up-checkbox-list.component.sass'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => UpCheckboxListComponent),
      multi: true
    }
  ]
})
export class UpCheckboxListComponent implements ControlValueAccessor {
  @HostBinding('class.UpCheckboxListComponent') containerClass = true;
  @Input() disabled: boolean;
  @ContentChildren(UpCheckboxListItemComponent, { descendants: true })
  options: QueryList<UpCheckboxListItemComponent>;

  selectedItems: string[];
  checkedItemsMapping: { [key: string]: boolean } = {};

  constructor() {}

  generateMapping() {
    this.checkedItemsMapping =
      this.selectedItems?.reduce((acc, value) => {
        return { ...acc, [value]: true };
      }, {}) || {};
  }

  valueChanged(event, value) {
    this.selectedItems = this.selectedItems || [];
    if (event.target.checked) {
      this.selectedItems = Array.from(new Set([...this.selectedItems, value]));
    } else {
      this.selectedItems = this.selectedItems.filter((item) => item !== value);
    }
    this.generateMapping();
    this.onChange(this.selectedItems);
  }

  onChange = (_) => {};
  onBlur = (_) => {};

  writeValue(obj: string[]): void {
    this.selectedItems = obj;
    this.generateMapping();
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onBlur = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
}
