import { Component, HostListener, Input } from '@angular/core';
import { upIcon } from '../../icons/lib';
import { UpPopOutService } from '../pop-out.service';

@Component({
  selector: 'up-pop-out-option',
  templateUrl: './pop-out-option.component.html',
  styleUrls: ['./pop-out-option.component.sass']
})
export class UpPopOutOptionComponent {
  @Input() danger = false;
  @Input() icon?: upIcon;
  @Input() iconWidth = '24px';
  @Input() iconHeight = '24px';
  @Input() disabled = false;
  @Input() dataTest = '';

  constructor(private dropdownService: UpPopOutService) {}

  @HostListener('click')
  emitClickEvent() {
    if (this.disabled) {
      return;
    }
    this.dropdownService.optionClick.next({});
  }

  get iconColor(): string {
    if (this.disabled) {
      return 'var(--up-neutral-400)';
    }
    return this.danger ? 'var(--up-negative-600)' : 'var(--up-brand-1-600)';
  }
}
