import { Component, Input } from '@angular/core';

@Component({
  selector: 'up-step-label',
  templateUrl: 'step-label.component.html',
  styleUrls: ['step-label.component.sass']
})
export class UpStepLabelComponent {
  @Input()
  stepLabelText: string = '';

  @Input()
  stepCompleted: boolean = false;

  get isStepCompleted(): boolean {
    return this.stepCompleted !== false;
  }

  get checkMarkIconColor(): string {
    if (this.isStepCompleted) {
      return 'var(--up-brand-3-400)';
    }
  }

  get labelTextClass(): string {
    return this.isStepCompleted ? 'up-step-label-completed' : 'up-step-label-default';
  }

  get stepLabelContainerClass(): string {
    return this.isStepCompleted ? 'up-step-label-completed-container' : 'up-step-label-container';
  }
}
